<template>
  <div id="buy-token" class="buy-token-modal mkp-modal">
    <div class="mkp-modal-content">
      <div class="mkp-modal-close" @click="close"></div>
      <div class="mkp-token-icon-large"></div>
      <h2><span class="color-primary">MKP</span> Token</h2>
      <div class="buy-form">
        <div class="form-input">
          <label>Your Email address:</label>
          <input name="email" type="email" autocomplete="on" />          
        </div> 
        <div class="form-input">
          <label>Your Ethereum wallet address for tokens:</label>
          <input name="text" type="text" autocomplete="on" />
        </div> 
        <div class="form-input">
          <label>Amount of tokens to buy: </label>
          <div class="row">
            <div class="col col-input">
              <input name="email" type="number" autocomplete="on" />
            </div>
            <div class="col col-price">
              <sub>Price: <strong>$ 3.15 USD</strong> per <strong>1 token</strong><sub>0,02401816 <strong>BTC</strong></sub></sub>
            </div>
          </div>
        </div>
        <hr class="dashed" />
        <div class="info-success">Total: <span class="price">$ 31.5</span></div>
        <div class="select-payment">
          <label>Choose your payment method:</label>
          <div class="payment-group">
            <div class="payment-item btc selected">BTC</div>
            <div class="payment-item ltc">LTC</div>
            <div class="payment-item eth">ETH</div>
            <div class="clearfix"></div>
          </div>
        </div>        
        <button class="mkbtn mkbtn-primary mkbtn-block" @click="submit">Submit</button>
        <div class="terms">
          <a href="">Minekamp Terms of Use</a>
        </div>
        <hr class="dashed before-terms" />
      </div>
      <div class="buy-result buy-result--success" style="display: none;">
        <h3>Thank you for your purchase!</h3>
        <button class="mkbtn mkbtn-primary mkbtn-block" @click="repeat">Repeat purchase</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods : {
    close() {
      $('#buy-token').hide();
    },
    submit() {
      $('.buy-form').hide();
      $('.buy-result').show();
    },
    repeat() {
      $('.buy-form').show();
      $('.buy-result').hide();
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/modules/BuyTokenModal.scss";
</style>



