import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '../components/NotFound'
import Home from '@/containers/Home'
import FAQ from '@/components/FAQ'
import Contacts from '../components/Contacts'
import ContactUs from '../components/ContactUs'
import Login from '@/containers/Login'
import Register from '@/containers/Register'
import ForgotPassword from '@/containers/ForgotPassword'
import Cabinet from '@/containers/Cabinet';
import Token from '@/components/Token';

import BlockExplorer from '@/components/cabinet/BlockExplorer'
import Monitoring from '@/components/cabinet/Monitoring';
import Settings from '@/components/cabinet/Settings';
import Support from '@/components/cabinet/Support';

import StratumGeneratorExpanded from '@/containers/StratumGeneratorExpanded';
import MiningCalculatorExpanded from '@/containers/MiningCalculatorExpanded';

// unknow from
import WalletManager from '@/components/WalletManager';

// temp
import TempButtons from '@/components/temp/TempButtons';

Vue.use(Router)

export default new Router({
  //mode: 'hash',
  routes: [    
    {
      path : '/temp/buttons',
      name : 'buttons',
      component: TempButtons
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/news',
      name: 'News',
      component: NotFound
    },
    {
      path: '/forum',
      name: 'Forum',
      component: NotFound
    },
    {
      path: '/faq',
      name: 'Faq',
      component: FAQ
    },
    {
      path: "/token",
      name: "Token",
      component: Token
    },
    {
      path: '/contacts',
      name: 'Contacts',
      component: ContactUs
    },
    {
      path: '/developing',
      component: NotFound
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/forgot_password',
      name: 'forget_password',
      component: ForgotPassword
    },
    /*
    {
      path : '/stratum-generator-expanded',
      name : 'StratumGeneratorExpanded',
      component: StratumGeneratorExpanded
    },*/
    {
      path : '/mining-calculator-expanded',
      name : 'MiningCalculatorExpanded',
      component : NotFound //MiningCalculatorExpanded
    },
    {
      path: '/cabinet',
      name: 'Cabinet',
      component: Cabinet,
      children: [
        {
          path: 'block-explorer',
          name: 'BlockExplorer',
          component: BlockExplorer
        },
        {
          path: 'monitoring',
          name: 'Monitoring',
          component: Monitoring
        },
        {
          path: 'settings',
          name: 'Settings',
          component: Settings
        },
        {
          path: 'support',
          name: 'Support',
          component: Support
        },
      ]
    }    
  ]
})



