<template>
  <div class="form-input">
    <label>{{title}}</label>
    <div class="input-with-btn-icon">
      <input ref="password" type="password" />
      <img 
        @mousedown="showPassword"
        @mouseup="hidePassword"
        @mouseout="hidePassword"
        src="/static/img/closed-eye.png" 
        srcset="/static/img/closed-eye@2x.png 2x, /static/img/closed-eye@3x.png 3x"
        class="password-eye"  />
    </div>
  </div>
</template>

<script>
export default {
  props : {
    title : String
  },
  methods : {
    getValue() {
      return this.$refs.password.value;
    },
    showPassword: function($event) {
      $($event.currentTarget).attr({
        src : "/static/img/open-eye.png",
        srcset : "/static/img/open-eye@2x.png 2x, /static/img/open-eye@3x.png 3x",        
      })
      .parent().find('input').attr({ type : "text" });
    },
    hidePassword: function($event){
      $($event.currentTarget).attr({
        src : "/static/img/closed-eye.png",
        srcset : "/static/img/closed-eye@2x.png 2x, /static/img/closed-eye@3x.png 3x",        
      })
      .parent().find('input').attr({ type : "password" });
    }
  }
}
</script>

<style lang="scss">
  @import "@/styles/form.scss";
  
  .input-with-btn-icon {
    position: relative;

    .password-eye {
      position: absolute;
      right: 12px;
      top: 14px;
      cursor: pointer;
    }
  }
</style>



