<script>
import BuyToken from '@/components/BuyToken.vue';
import BuyTokenModal from '@/components/BuyTokenModal.vue';

export default {
  components : {
    BuyToken,
    BuyTokenModal
  },
  methods: {
    openBuyModal() {
      $('.buy-form').show();
      $('.buy-result').hide();
      $('#buy-token').show();
    },
    closeBuyModal() {
      $('#buy-token').hide();
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/modules/Token.scss";
</style>



