<template>
  <div id="personal-settings">
    <div class="row-wallets clearfix">
      <div class="col-virtual">
        <VirtualWallets />
      </div>
      <div class="col-external">
        <ExternalWallets />
      </div>
    </div>
    <div class="row-change clearfix">
      <div class="col-change-password">
        <ChangePassword />
      </div>
      <div class="col-change-email">
        <ChangeEmail />    
      </div>
    </div>
    <div class="watch-account-credentials-container">
      <WatchAccountCredentials />
    </div>
    <div class="row-authentication-and-security clearfix">
      <div class="col-authentication">
        <TwoFactorAuthentication />
      </div>
      <div class="col-security">
        <Security />
      </div>
    </div>
    <ApiKey />
  </div>
</template>

<script>
import VirtualWallets from '@/components/cabinet/settings/VirtualWallets';
import ExternalWallets from '@/components/cabinet/settings/ExternalWallets';
import ChangePassword from '@/components/cabinet/settings/ChangePassword';
import ChangeEmail from '@/components/cabinet/settings/ChangeEmail';
import WatchAccountCredentials from '@/components/cabinet/settings/WatchAccountCredentials';
import TwoFactorAuthentication from '@/components/cabinet/settings/TwoFactorAuthentication';
import Security from '@/components/cabinet/settings/Security';
import ApiKey from '@/components/cabinet/settings/ApiKey';

export default {
  name : 'CabinetSettings',

  components : {
    VirtualWallets,
    ExternalWallets,
    ChangePassword,
    ChangeEmail,
    WatchAccountCredentials,
    TwoFactorAuthentication,
    Security,
    ApiKey
  }
}
</script>

<style lang="scss">
  @import "../../styles/cabinet/settings.scss";
</style>



