<template>
  <div id="miningCalculator" class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">Mining calculator</h3>
      <div class="mk-icon-btn mk-icon mk-icon-18 mk-icon-maximize right" style="margin-right: 10px;"
        @click="mining_calculator_expand">
      </div>
      <div class="mk-right-question have-hint">
        <i class="mk-panel-header__question"></i>
        <div class="hint" style="display:none;">
          <div class="hint-triangle"></div>
          <div class="hint-content">
            <p>See what algo is making more money here.</p>
            <p>For a full version please press        button</p>
            <p>Typically, we take the last 250 shares, calculate their dollar value, and update the graph.</p>
            <p>Please note that this graph just gives an approximation of what you may be earning.</p>
            <p>The real numbers may be a the same or lower for large miners and higher for small miners (for various reasons). If you want to earn more, please ensure to have fast internet connection and  miners that restart quickly.</p>
          </div>
        </div>
      </div>
      <div class="clearifx"></div>
    </div>
    <div class="mk-panel-body">
      <div class="mk-btn-group clearfix">
        <button type="button">SHA-256</button>
        <button type="button" class="active">Scrypt</button>
        <button type="button">x11</button>
      </div>

      <input-combo-select label="Hashrate" :listItems="hashRateItems" />
      <input-combo-select label="Power consumption" :listItems="powerConsumptionItems" />
      <div class="text-combo-select">
        <label>Power cost</label>
        <input type="text" placeholder="Type here" />
        <div class="combo-select">
          <div class="combo-select-value">
            USD
          </div>
        </div>
      </div>
      <input-combo-select label="Difficulty" :listItems="difficultyItems" />
      <div class="text-combo-select">
        <label>Hardware cost</label>
        <input type="text" placeholder="Type here" />
        <div class="combo-select">
          <div class="combo-select-value">
            USD
          </div>
        </div>
      </div>

      <div class="mk-info">
        $20/day
        <br /><br />
        $600/month
        <br /><br />
        <strong>Breakeven:</strong> 2 months 23 days 
      </div>      
      <button id="btn-calculate" class="mkbtn mkbtn-round-large mkbtn-block mkbtn-primary">Calculate</button>
    </div>
  </div>
</template>

<script>
import InputComboSelect from '@/components/elements/InputComboSelect';

export default {
  components : {
    InputComboSelect
  },
  mounted() {
    $('.have-hint').click(function(e){
      $(this).find('.hint').toggle();
    });
  },
  data: function() {
    return {      
      hashRateItems : [
        { id: 1, title: 'GH/s' },
        { id: 2, title: 'GH/s' }
      ],
      powerConsumptionItems: [
        { id: 1, title: 'W' },
        { id: 2, title: 'kW' }
      ],
      difficultyItems: [
        { id: 1, title: 'Multi' },
        { id: 2, title: 'Single' }
      ]
    }
  },
  methods : {
    mining_calculator_expand(e) {
      this.$router.push('mining-calculator-expanded');
    }
  }
}
</script>

<style lang="scss">
  #btn-calculate {
    margin-top: 30px;
  }
</style>



