<template>
  <div class="btn-group-tab">
    <button v-for="btn in buttons" :key="btn.id" @click.prevent="on_click(btn, $event)">
      {{btn.caption}}
    </button>
  </div>
</template>

<script>
export default {
  props : {
    buttons : Array,
    onChange : Function
  },
  data() {
    return {
      currentButton : null
    }
  },
  methods : {
    on_click(sender, e) {
      if (this.currentButton !== null && this.currentButton !== $(e.target)) {
        this.currentButton.removeClass('active');        
      }
      if (this.currentButton === null || this.currentButton !== $(e.target)) {
        if (this.onChange) {
          this.onChange(sender);
        }
      }
      this.currentButton = $(e.target);
      this.currentButton.addClass('active');
    }
  }
}
</script>

<style lang="scss">
  .btn-group-tab {
    button {
      border: solid 1px #0a9bbe;
      color: #036f9f;
      background-color: #ffffff;
      width: 108px;
      height: 33px;
      font-size: 12px;

      &:first-child {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
      }
      &:last-child {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
      }
      &:not(:first-child) {
        margin-left: -1px;
      }

      &.active {
        background-color: #0a9bbe;
        color: #ffffff;
      }
    }
  }
</style>



