<template>
  <i class="mk-panel-header__question hint-closed" @click="toggle">
    <div class="hint">
      <div class="hint-triangle"></div>
      <div class="hint-content" v-html="text"></div>
    </div>
  </i>
</template>

<script>
export default {
  props : {
    text : String
  },
  methods : {
    toggle(e) {
      $(e.currentTarget)
        .toggleClass('hint-closed')
        .toggleClass('hint-opened');
    }
  }
}
</script>

<style lang="scss">
  .hint-closed {
    .hint {
      display: none;
    }
  }
  .hint-opened {
    .hint {
      display: block;
    }
  }
</style>



