<template>
  <div id="virtual-wallets">
    <div class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          Virtual wallets
          <HintQuestion :text="hints.top" />
        </h3>
      </div>
      <div class="mk-panel-body clearfix">
        <div class="table-wallets">

          <div class="table-wallets_header">
            <div class="table-wallets-row">
              <div class="table-wallets-cell withdrawal">
                Withdrawal
                <HintQuestion :text="hints.top" />
              </div>
              <div class="table-wallets-cell current-holdings">
                Current
                <HintQuestion :text="hints.top" />
                <br />holdings
              </div>
              <div class="table-wallets-cell network-fees">
                Network
                <HintQuestion :text="hints.top" />
                <br />fees                
              </div>
            </div>
          </div>

          <div class="table-wallets_body">
            <div class="row-container" v-for="item in virtualWallets" :key="item.id">
              <div class="table-wallets-row">
                <div class="table-wallets-cell withdrawal">

                  <div class="check-coin">
                    <div class="check-coin-checkbox">
                      <Checkbox />
                    </div>
                    <div class="check-coin-icon">
                      <i class="icon-coin-24" :class="getCoinClass(item.coin)"></i>
                    </div>
                    <div class="check-coin-label">
                      {{item.coin.caption}}
                    </div>
                  </div>
                </div>
                <div class="table-wallets-cell current-holdings">
                  {{item.holdings.value}}
                  <br />
                  {{item.holdings.currency.symbol}} {{item.holdings.price}}
                </div>
                <div class="table-wallets-cell network-fees">
                  {{item.networkFees}}
                </div>
              </div>
              <div class="row-container__inner-row">
                <div class="button-with-label-container">
                  <div class="button">
                    <button class="mkbtn mkbtn-warning mkbtn-small">
                      <i class="mkbtn-icon mkbtn-icon--refresh"></i>
                    </button>
                  </div>
                  <div class="label">
                    Deposit wallet
                  </div>
                </div>
              </div>
              <div class="row-container__inner-row">
                <BorderedSelect />
              </div>
              <div class="row-container__inner-row">                
                <div class="wallet-value">
                  <label>Wallet:</label> 
                  {{item.wallet}}
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="virtual-balance">
          <div class="virtual-balance__header">
            Virtual balance
          </div>
          <div class="virtual-balance-row clearfix">
            <div class="col col-select">
              <div class="row-first">
                <div class="selected-coin-icon icon-litecoin"></div>
                <BorderedSelect :items="coins" :class="[ 'text-center', 'bordered-select-sm' ]" />
              </div>
            </div>
            <div class="col col-one">
              <div class="row-first">0.1</div>
              <div class="row-second">$ 1000</div>
            </div>
            <div class="col">
              <div class="col col-two">
                <div class="row-first"></div>
                <div class="row-second"></div>
              </div>
              <div class="col col-three">
                <div class="row-first">0.05</div>
                <div class="row-second">$ 1</div>
              </div>
            </div>
            <div class="col">
              <div class="col col-four">
                <div class="row-first">=</div>
                <div class="row-second"></div>
              </div>
              <div class="col col-result">
                <div class="row-first">0.95</div>
                <div class="row-second">$ 995</div>
              </div>
            </div>
          </div>
        </div> <!-- virtual-balance -->

        <div class="col-withdrawal">
          <p class="info">Withdrawal happens once a day, <strong>at 12:00 am EDT</strong></p>

          <div class="withdrawal-type-item">
            <Radio name="withdrawalType" label="Auto transfer when balance reach" class="mk-radio-sm" />
            <div class="balance-value clearfix">
              <div class="balance-value--money">
                <Money v-model="withdrawals.auto_transfer_when_balance.value" v-bind="withdrawals.auto_transfer_when_balance.money" class="mk-input text-right" />
              </div>
              <div class="balance-value--coin">
                <div class="selected-coin-icon icon-litecoin"></div>
                <BorderedSelect :items="coins" :class="[ 'text-center', 'bordered-select-sm' ]" />
              </div>
            </div>
          </div>

          <div class="withdrawal-type-item">
            <Radio name="withdrawalType" label="Auto transfer every:" class="mk-radio-sm" />
            <div class="week">
              <div class="week-day">
                <Checkbox label="Monday" />
              </div>
              <div class="week-day">
                <Checkbox label="Tuesday" />
              </div>
              <div class="week-day">
                <Checkbox label="Wednesday" />
              </div>
              <div class="week-day">
                <Checkbox label="Thursday" />
              </div>
              <div class="week-day">
                <Checkbox label="Friday" />
              </div>
              <div class="week-day">
                <Checkbox label="Saturday" />
              </div>
              <div class="week-day">
                <Checkbox label="Sunday" />
              </div>
            </div>
          </div>

          <div class="withdrawal-type-item">
            <Radio name="withdrawalType" class="mk-radio-sm" />
            <button class="mkbtn mkbtn-primary-light">Manual Transfer</button>
            <div class="manual-transfer-hint">
              Press button to initiate action
            </div>
          </div>

          <div class="withdrawal-type-item">
            <div class="donation">
              <div class="donation--caption">
                <h3>
                  Your kind Donation
                  <HintQuestion :text="hints.top" />
                </h3>
              </div>
              <div class="donation--value">
                <input class="mk-input text-right" /> %
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import MinekampApi from '@/api/MinekampApi';
import HintQuestion from '@/components/elements/HintQuestion';
import Checkbox from '@/components/elements/Checkbox';
import BorderedSelect from '@/components/elements/BorderedSelect';
import Radio from '@/components/elements/Radio';
import { Money } from 'v-money'

export default {
  components : {
    HintQuestion,
    Checkbox,
    BorderedSelect,
    Radio,
    Money
  },
  data(){
    return {
      hints : {
        top : "No text",
        withdrawal : "Withdrawal",
        currentHoldings : "Current holdings",
        networkFees : "Network fees"
      },
      coins: [
        {
          id: 1, name: 'BTC'
        },
        {
          id: 2, name: 'LTC'
        }
      ],
      virtualWallets : [],
      withdrawals : {
        auto_transfer_when_balance : {
          value : 100,
          money: {
            decimal: ',',
            thousands: '.',
            prefix: '$ ',
            //suffix: ' #',
            precision: 0,
            masked: false /* doesn't work with directive */
          }
        }
      }
    }
  },
  mounted() {
    let self = this;
    MinekampApi.virtualWallets(function(r){
      self.virtualWallets = r;
    })
  },
  methods : {
    getCoinClass(coin) {
      return "icon-" + coin.name;
    }
  }
}
</script>

<style lang="scss">
  @import "../../../styles/cabinet/VirtualWallets.scss";
</style>



