<template>
  <div class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">
        Change password
        <hint-question :text="hints.changePassword" />
      </h3>
    </div>
    <div class="mk-panel-body">        
      <form class="mk-form">
        <password-input ref="oldPassword" title="Old password" /> 
        <password-input ref="newPassword" title="New password" /> 
        <password-input ref="newPasswordRe" title="Repeat new password" />
        <vue-recaptcha
          ref="changePasswordReCaptcha"
          class="reCaptcha"
          @verify="on_chpr_verify"
          @expired="on_chpr_expired"
          :sitekey="sharedStore.reCaptchaSiteKey">
        </vue-recaptcha>
        <button class="mkbtn mkbtn-primary mkbtn-with-inline-icon" type="button" @click.prevent="changePassword">
          <i class="mkbtn-inline-icon mkbtn-inline-icon--success"></i>
          Save changes
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { WebApi } from '@/webapi';
import { SharedStore } from '@/shared-store';
import VueRecaptcha from 'vue-recaptcha';
import PasswordInput from '@/components/elements/PasswordInput';
import HintQuestion from '@/components/elements/HintQuestion';

export default {
  components : {
    VueRecaptcha,
    PasswordInput,
    HintQuestion
  },
  data() {
    return {
      sharedStore : SharedStore.state,
      hints : {
        changePassword : "No text"
      },
      form : {
        oldPassword : null,
        newPassword : null,
        newPasswordRe : null,
        captcha: null
      },
    }
  },
  methods : {
    on_chpr_verify(r) {
      this.form.captcha = r;
    },
    on_chpr_expired() {
      this.form.captcha = null;
    },
    changePassword(e) {
      let self = this;
      let target = e.currentTarget;

      self.$refs.changePasswordReCaptcha.reset();

      this.form.oldPassword = this.$refs.oldPassword.getValue();
      this.form.newPassword = this.$refs.newPassword.getValue();
      this.form.newPasswordRe = this.$refs.newPasswordRe.getValue();
      
      WebApi.changePassword(this.form, function(r){
        $(target).parent().find('.api-result').html(Utils.formatJson(r));
      });
    },
  }
}
</script>

<style>

</style>



