<template>
  <div class="buy-token-panel" id="buy-token-panel">
    <div class="token-icon"></div>
    <button class="mkbtn mkbtn-primary btn-buy-token" @click="onBuyClicked">BUY</button>
  </div>
</template>

<script>
export default {
  beforeMount () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods : {
    handleScroll (e) {
      var scrolled = window.pageYOffset || document.documentElement.scrollTop;
      let prev_top = $('#site-token').offset().top;
      $('#buy-token-panel').offset({ top: prev_top + scrolled });
    },
    onBuyClicked() {
      this.$emit('openBuyModal');
    }
  }
}
</script>

<style lang="scss">
  .buy-token-panel {
    position: absolute;
    right: 0;
    top: 0px;
    z-index: 9999;
    @media(min-width: 768px) {
      background-color: #ffffff;
      padding: 20px;
      box-shadow: 0 2px 15px 0 #bed0d7;
    }

    .token-icon {
      width: 42px;
      height: 42px;
      margin: 0 auto;
      background-image: url(/static/img/mpk-token.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      margin-bottom: 10px;
    }
    .btn-buy-token {
      width: 60px;
      @media(min-width: 768px){
        width: 120px;
      }
    }
  }
</style>



