<template>
  <div id="external-wallets" class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">
        External wallets
        <HintQuestion :text="hints.top" />
      </h3>
    </div>
    <div class="mk-panel-body">

      <div class="ext-wall--row clearfix">
        <div class="ext-wall--col">
          <h4>
            Choose wallets for withdrawal
            <HintQuestion :text="hints.top" />
          </h4>
        </div>
      </div>

      <div class="ext-wall--row clearfix">
        <div class="ext-wall--col">

        </div>
      </div>

    </div>
  </div>
</template>

<script>
import HintQuestion from '@/components/elements/HintQuestion';
import BorderedSelect from '@/components/elements/BorderedSelect';

export default {
  components : {
    HintQuestion,
    BorderedSelect  
  },
  data() {
    return {
      hints : {
        top : 'External wallets'
      }
    }
  }
}
</script>

<style lang="scss">
  @import "../../../styles/cabinet/ExternalWallets.scss";
</style>



