<template>
  <div id="sideBarNews" class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">News</h3>
      <div class="mk-panel-header__icons">
        <!--a href="" class="mk-panel-header__icon-button">
          <i class="icon-shape"></i>
        </a-->
        <a href="http://t.me/minekamp" target="_blank" class="mk-panel-header__icon-button">
          <i class="icon-social icon-telegram"></i>
        </a>
        <a href="https://twitter.com/minekamp" target="_blank" class="mk-panel-header__icon-button">
          <i class="icon-social icon-twitter"></i>
        </a>
        <a href="https://www.instagram.com/minekamp" target="_blank" class="mk-panel-header__icon-button">
          <i class="icon-social icon-instagram"></i>
        </a>
        <a href="https://www.youtube.com/channel/UCiiwJ1Nqw2FKTbhU85_Tr8g" target="_blank" class="mk-panel-header__icon-button">
          <i class="icon-social icon-youtube"></i>
        </a>
        <!--a href="" class="mk-panel-header__icon-button">
          <i class="icon-reddit"></i>
        </a>
        <a href="" class="mk-panel-header__icon-button">
          <i class="icon-facebook"></i>
        </a>
        <a href="" class="mk-panel-header__icon-button">
          <i class="icon-bitcoin"></i>
        </a-->
      </div>
    </div>
    <div class="mk-panel-body">
      <div class="news-item" v-for="item in news" :key="item.id">
        <h5 class="news-date">{{item.date}}</h5>
        <h4 class="news-caption">{{item.caption}}</h4>
        <div class="news-body" v-html="item.body"></div>
      </div>
    </div>
    <div class="mk-panel-footer text-center">
      <a href="/News" class="mk-btn-more">See all&nbsp;&nbsp;<i class="fas fa-angle-right"></i></a>
    </div>
  </div>
</template>

<script>
export default {
  data : function(){
    return {
      news : [
        {
          id: 1,
          date: '21.10.18',
          
          caption: 'Hi there!',
          body: 'We are going to launch!<br />Just some thing left to do.'
        }
        /*,
        {
          id: 2,
          date: '12.09.18',
          caption: 'Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore',
          body: 'Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.'
        },
        {
          id: 3,
          date: '11.09.18',
          caption: 'Excepteur sint occaecat cupidatat non proident',
          body: 'Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt.'
        }*/
      ]
    }
  }
}
</script>

<style lang="scss">
  .news-item {
    margin-bottom: 20px;
    .news-date {
      font-size: 12px;
      line-height: 1.33;
      color: #0f0f0f;
    }
    .news-caption {
      font-size: 14px;
      font-weight: 600;
      line-height: 1.5;
      color: #0f0f0f;
    }
    .news-body {
      margin-top: 14px;
      font-size: 12px;
      color: #787878;

      p+p {
        margin-top: 14px;
      }
    }
  }
  .news-item + .news-item {
    margin-top: 39px;
  }
  .mk-panel-header__icons {
    margin-top: 19px;
    display: block;

    .mk-panel-header__icon-button {      

      i {
        display: inline-block;
        width: 20px;
        height: 20px;
        background-position: center;
        background-repeat: no-repeat;
      }

      .icon-social {
        background-position: 0 0;
        background-repeat: no-repeat;

        &:hover {
          background-position: -20px 0;
        }
      }

      .icon-shape {
        background-image: url(/static/img/shape.svg);
      }
      .icon-reddit {
        background-image: url(/static/img/reddit-1.svg);
      }

      .icon-telegram {
        background-image: url(/static/img/socials/telegram.svg);
      }
      .icon-instagram {
        background-image: url(/static/img/socials/instagram.svg);
      }
      .icon-youtube {
        background-image: url(/static/img/socials/youtube.svg);
      }
      .icon-twitter {
        background-image: url(/static/img/socials/twitter.svg);
      }


      .icon-facebook {
        background-image: url(/static/img/facebook.svg);
      }
      .icon-bitcoin {
        background-image: url(/static/img/bitcoin-logo.svg);
      }
    }
    .mk-panel-header__icon-button + .mk-panel-header__icon-button {
      margin-left: 21px;
    }
  }

</style>



