<template>  
  <div id="home" class="clearfix">
    <mobile v-if="currentTemplate === null" />
    <tablet768 v-if="currentTemplate === 'tablet_768'" />
    <tablet1024 v-if="currentTemplate === 'tablet_1024'" />
    <desktop v-if="currentTemplate === 'desktop'" />
  </div>
</template>
<script>

import { AuthStore } from '@/auth-store';

import Mobile from '@/components/displays/home/Mobile';
import Tablet768 from '@/components/displays/home/Tablet768';
import Tablet1024 from '@/components/displays/home/Tablet1024';
import Desktop from '@/components/displays/home/Desktop';

export default {
  components: {
    Mobile,
    Tablet768,
    Tablet1024,
    Desktop
  },
  data : function() {
    return {
      currentTemplate: null,
      authState : AuthStore.state
    }
  },
  mounted : function(){
    this.handleResize();
    window.addEventListener('resize', this.handleResize);

    $('.have-hint').click(function(e){
      $(this).find('.hint').toggle();
    });
  },
  beforeDestroy : function(){
    window.removeEventListener('resize', this.handleResize);
  },
  methods : {
    handleResize: function(){
      var width = document.documentElement.clientWidth;

      if (0 < width && width < 768) { // mobile        
        if (this.currentTemplate !== null) {
          this.currentTemplate = null;
        }
      }
      if (768 <= width && width < 1024) { // table 768
        if (this.currentTemplate !== 'tablet_768') {
          this.currentTemplate = 'tablet_768';
        }
      }
      if (1024 <= width && width < 1340) { // table 1024        
        if (this.currentTemplate !== 'tablet_1024') {
          this.currentTemplate = 'tablet_1024';
        }
      }
      if (1340 <= width) {// desktop        
        if (this.currentTemplate !== 'desktop') {
          this.currentTemplate = 'desktop';
        }
      }
    }
  }
}
</script>

<style lang="scss">

</style>


