import axios from 'axios';
import { AuthStore } from '@/auth-store';

const Statuses = {
  success : "success",
  error: "error",
  need2fa: "need2fa",
}

function Result(theIsSuccess, theMessage, theData) {
  this.isSuccess = theIsSuccess;
  this.message = theMessage;
  this.data = theData;
}
function Results() {

}
Results.prototype.ok = function(theData) {
  return new Result(true, null, theData);
}
Results.prototype.fail = function(theMessage) {
  return new Result(false, theMessage, null);
}


export const WebApi = {

  uri_resigeter                   : 'https://minekamp.com/api/cabinet/register',
  uri_login                       : 'https://minekamp.com/api/cabinet/login',
  uri_restore_password            : 'https://minekamp.com/api/cabinet/restore_password',
  uri_change_email                : 'https://minekamp.com/api/cabinet/change_email',  
  uri_change_password             : 'https://minekamp.com/api/cabinet/change_password',
  uri_change_password_reset_email : 'https://minekamp.com/api/cabinet/change_password_reset_email',
  uri_2auth                       : 'https://minekamp.com/api/cabinet/2auth',
  uri_2auth_test                  : 'https://minekamp.com/api/cabinet/2auth_test',
  uri_prevent_top                 : 'https://minekamp.com/api/cabinet/prevent_tor',

  uri_send_message                : 'https://shop.minekamp.com/api/buyer_message',

  token : null,

  setToken(the_token) {
    this.token = the_token;
    $.ajaxSetup({
      beforeSend: function(xhr) {
          xhr.setRequestHeader('Authorization', 'Bearer ' + the_token);
      }
    });
  },

  _post(uri, prm, callback) {
    console.log(prm);
    $.post(uri, prm, function(r){
      console.log(r);
      callback(r);
    })
  },

  _auth_post(uri, prm, callback) {
    let token = AuthStore.getToken();
    if (token) {
      console.log('set token: ' + token);
      this.setToken(token);
      this._post(uri, prm, callback);
    } else {
      throw new Exception("Token is empty or expired");
    }   
  },

  register({ login, email, password, passwordRe, captcha }, callback ) {
    let self = this;

    let prm = {
      'userNick' : login,
      'userEmail' : email,
      'userPassword' : password,
      'repeatPassword' : passwordRe,
      'g-recaptcha-response' : captcha
    };
    
    self._post(this.uri_resigeter, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  login({ login, password, captcha }, callback) {
    let prm = {
      'userEmail' : login,
      'userPassword' : password,
      'g-recaptcha-response' : captcha
    };

    this._post(this.uri_login, prm, function(r){
      if (r.Status === Statuses.success) {
        callback(new Result(true, null, { token: r.Token, settings : r.Settings, need2fa : false }));
      } else if (r.Status === Statuses.need2fa) {
        callback(new Result(true, null, { need2fa : true }));
      } else {
        callback(new Result(false, r.Message));
      }      
    });
  },

  login2fa({ login, password, rememberMe, captcha, googleCode }, callback) {
    let self = this;

    let prm = {
      'userEmail' : login,
      'userPassword' : password,
      'rememberMe' : rememberMe,
      'g-recaptcha-response' : captcha,
      'token2fa' : googleCode
    };

    self._post(this.uri_login, prm, function(r){
      self._callback_result(r, callback, { token : r.Token, settings : r.Settings });
    });
  },

  restorePassword({ email, captcha }, callback) {   
    let self = this;

    let prm = {
      'userEmail' : email,
      'g-recaptcha-response' : captcha
    };

    self._auth_post(this.uri_restore_password, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  changeEmail({ password, email, captcha }, callback) {
    let self = this;

    let prm = {
      'userEmail' : email,
      'userPassword' : password,
      'g-recaptcha-response' : captcha
    };

    self._auth_post(this.uri_change_email, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  changePassword({ oldPassword, newPassword, newPasswordRe, captcha }, callback) {
    let self = this;
    let prm = {
      'oldPass' : oldPassword, 
      'newPass' : newPassword, 
      'repeatPass' : newPasswordRe,
      'g-recaptcha-response' : captcha
    };
    self._auth_post(this.uri_change_password, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  changePasswordResetEmail({ password, email, captcha }, callback) {
    let self = this;
    let prm = {
      'userResetEmail' : email,
      'userPassword' : password,
      'g-recaptcha-response' : captcha
    };
    self._auth_post(this.uri_change_password_reset_email, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  turn_2fa_on(callback) {
    let self = this;

    self._auth_post(this.uri_2auth, { turn_on: true }, function(r){
      self._callback_result(r, callback, { base64image : "data:image/png;base64, " + r.FA2.Png });
    });
  },

  turn_2fa_on_confirm({ code, captcha }, callback) {
    let self = this;
    let prm = {
      'token' : code,
      'g-recaptcha-response' : captcha
    };
    self._auth_post(this.uri_2auth_test, prm, function(r){
      self._callback_result(r, callback);
    });
  },

  turn_2fa_off(callback) {
    let self = this;
    self._auth_post(this.uri_2auth, { turn_on: false }, function(r){
      self._callback_result(r, callback);
    });
  },

  prevent_tor(value, callback) {
    let self = this;
    self._auth_post(this.uri_prevent_top, { prevent_tor : value }, function(r){
      self._callback_result(r, callback);
    });
  },

  _callback_result(r, callback, data) {
    data = typeof data !== 'undefined' ?  data : null;
    
    if (r.Status === Statuses.success) {
      callback(new Result(true, null, data));
    } else {
      callback(new Result(false, r.Message, data));
    }
  },

  contact_us({ name, email, message, captcha }, callback) {
    let self = this;

    let user = {
      "name": name,      // 200 len
      "email": email,    //   200 
      "message": message //   10000 
    };

    let data = new FormData();
    data.append('g-recaptcha-response', captcha);
    data.append('data', JSON.stringify(user));

    $.ajax({
      url: self.uri_send_message,
      data: data,
      processData: false,
      contentType: false,
      type: 'POST',
      success: function(r){
        if (r.Status === Statuses.success) {
          callback(new Result(true, "Message sent successfully!", null));
        } else {
          callback(new Result(false, r.Message, null));
        }
      }
    });

  }
}


