<template>
  <div id="stratumGenerator">
    <div class="mk-panel">
      <div class="mk-panel-header">      
        <h3 class="mk-panel-title">Stratum generator</h3>
        
        <div class="mk-icon mk-icon-18 mk-icon-question right mk-icon-btn have-hint">
          <div class="hint" style="display:none;">
            <div class="hint-triangle"></div>
            <div class="hint-content">
              <p>See what algo is making more money here.</p>
              <p>For a full version please press        button</p>
              <p>Typically, we take the last 250 shares, calculate their dollar value, and update the graph.</p>
              <p>Please note that this graph just gives an approximation of what you may be earning.</p>
              <p>The real numbers may be a the same or lower for large miners and higher for small miners (for various reasons). If you want to earn more, please ensure to have fast internet connection and  miners that restart quickly.</p>
            </div>
          </div>
        </div>

        <div class="mk-icon-btn mk-icon mk-icon-18 mk-icon-maximize right" style="margin-right: 10px;"
          @click="stratum_generator_expand">
        </div>
      </div>
      <div class="mk-panel-body">
        <div class="mk-btn-group clearfix">
          <button type="button" class="active">SHA-256</button>
          <button type="button">Scrypt</button>
          <button type="button">x11</button>
        </div>
        <p>If you are registered, please use your login</p>
        <ul class="mk-panel-tabs">
          <li class="active">
            <a href="#">
              Login
            </a>
          </li>
          <li>
            <a href="#">
              Payment wallet
            </a>
          </li>
        </ul>
        <div class="clearfix"></div>
        <div id="stratum-generator-login">
          <input class="mk-panel-input" type="text" placeholder="Type here" />
          <mk-select label="Server" :listItems="servers" />
        </div>
        <div class="mk-info">
          <strong>Link:</strong> minekamp.com:836
          <br /><br />
          <strong>Login:</strong> abdfkj47hf
          <br /><br />
          <strong>Pass:</strong> x=SHA256 d=AUTO L=NONE
        </div>      
        <button id="btn-generate" class="mkbtn mkbtn-round-large mkbtn-block mkbtn-primary">Generate</button>
        <div class="need-new-wallet">
          <router-link :to='{ path: "/developing" }'>Need a new wallet?</router-link>
        </div>
      </div>
    </div>    
  </div>
</template>

<script>
import MkSelect from '@/components/elements/MkSelect';

export default {
  components : {
    MkSelect
  },
  data: function(){
    return {
      servers : [
        { id: 0, name: 'AUTO', icon: 'mk-icon-any' },
        { id: 1, name: 'New York', icon: 'mk-icon-any'},
        { id: 2, name: 'China', icon: 'mk-icon-any' }
      ]
    }
  },
  methods : {
    stratum_generator_expand : function(){
      this.$router.push('stratum-generator-expanded');
    }
  }
}
</script>

<style lang="scss">
  .need-new-wallet {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;

    a {
      font-size: 16px;
      
      color: #005268;
    }
  }
  #stratum-generator-login {
    input {
      margin-bottom: 30px;
    }
  }
  #btn-generate {
    margin-top: 30px;
  }
</style>



