<template>  
  <div class="header-currency">

    <div class="hc-col-price-value">
      <div class="current-price-value" @click="showSelectCurrentCurrency()">
        {{currentCurrency}} <i class="fas fa-angle-down"></i>
        <div class="select-current-price-values" style="display:none;">
          <div class="hint-tiangle"></div>
          <div class="select-current-price-values-content">
            <div class="select-current-currency" 
              @click="selectCurrentCurrency(item)"
              v-for="item in currentCurrencies" :key="item">
              {{item}}
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>      
    </div>

    <div class="hc-col-coins">
      <div class="hc-col-coin" v-for="item in exchangePrices" :key="item.Coin">
        <div class="header-currency-coin">
          {{item.Coin}}: 
          <span class="header-currency-coin-value" :class="{ 'price-value-grow' : priceHasGrow(item), 'price-value-fall' : priceHasFall(item) }">
            {{ getPriceValue(item) }}
            <i class="price-change" :class="{ 'price-grow' : priceHasGrow(item), 'price-fall' : priceHasFall(item) }"></i>
          </span>
        </div>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="clearfix"></div>

    <div ref="btn_toggle" class="currency__toggle" v-on:click="toggle"></div>
  </div>

</template>

<script>
//import VueWebsocket from "vue-websocket";
// Vue.use(VueWebsocket, "wss://minekamp.com/api/exchrate");

export default {
  data: function() {
    return {
      currentCurrencies: [
        "USD", "EUR", "BTC"
      ],
      currentCurrency : "USD",
      exchangePrices: []
    }
  },
  created() {
    let self = this;
    var socket = new WebSocket("wss://minekamp.com/api/exchrate");
    socket.onmessage = function(event) {
      self.updateCurrencies(JSON.parse(event.data));
    };
  },
  methods : {
    toggle: function (e) {
      $('.header-currency')
        //.toggleClass('header-currency--collapsed')
        .toggleClass('header-currency--expanded');
    },
    updateCurrencies(data) {
      // console.log('received:');
      // console.log(data);
      if (data.Status === "success") {
        this.exchangePrices = data.ExchangePrices.splice(0, 7);
        //for(let i = 0; i < data.ExchangePrices.length; i++) {
          //let coin = data.ExchangePrices[i];
          
          /*
          if (coin.Coin && coin.Prices) {            
            let price = getPrice(this.currentCurrency, coin.Prices);
            if (price && price.value && price.dir) {
              $('.header-currency-coin.' + coin.Coin)
                .find('span')
                .html(price.value + '<i class="price-change price-dir' + price.dir + '"></i>');
            }
          }*/
        //}
      }
      function getPrice(currency, values) {
        // console.log('prices:');
        // console.log(values);

        for(let i = 0; i < values.length; i++) {
          if (values[i].Curr.toLowerCase() === currency.toLowerCase()) {
            return { value: parseFloat(values[i].Value).toFixed(4), dir: values[i].Dir };
          }
        }
        return null;
      }
    },
    getPriceValue(exchengePrice) {
      for(let i = 0; i < exchengePrice.Prices.length; i++) {
        if (exchengePrice.Prices[i].Curr.toLowerCase() === this.currentCurrency.toLowerCase()) {
          return parseFloat(exchengePrice.Prices[i].Value).toFixed(4)
        }
      }
      return 0.0;
    },
    priceHasGrow(exchengePrice) {
      for(let i = 0; i < exchengePrice.Prices.length; i++) {
        if (exchengePrice.Prices[i].Curr.toLowerCase() === this.currentCurrency.toLowerCase()) {
          return exchengePrice.Prices[i].Dir === 1;
        }
      }
      return false;
    },
    priceHasFall(exchengePrice) {
      for(let i = 0; i < exchengePrice.Prices.length; i++) {
        if (exchengePrice.Prices[i].Curr.toLowerCase() === this.currentCurrency.toLowerCase()) {
          return exchengePrice.Prices[i].Dir === -1;
        }
      }
      return false;
    },
    showSelectCurrentCurrency() {
      $('.select-current-price-values').toggle();
    },
    selectCurrentCurrency(item) {
      this.currentCurrency = item;
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/var.scss";

  .header-currency {
    display: block;
    position: relative;
    //min-height: 72px;    
    background-color: #ffffff;
    box-shadow: 0 2px 13px 0 #a9dce7;
    margin-bottom: 20px;
    overflow: hidden;

    max-height: 22px;
    transition: max-height 0.2s ease-out;

    @media (min-width: $tablet-min-width) {
      max-height: 1000px;
      height: auto;
      transition: none;
      padding-top: 15px;
      padding-bottom: 22px;
    }

    .currency__toggle {
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 32px;
      height: 8px;
      background-image: url(/static/img/to-curtail.svg);
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
      margin-left: -16px;

      @media (min-width: $tablet-min-width) {
        display: none;
      }
    }

    &.header-currency--expanded {
      max-height: 1000px;
      transition: max-height 0.2s ease-in;
    }

    .hc-col-price-value {   
      float: left;
      width: 15%;
      
      @media (max-width: $tablet-min-width){
        //width: 100%;
      }

      .current-price-value {
        text-align: center;
        cursor: pointer;
        position: relative;
        width: 83px;
        height: 29px;
        border-radius: 6px;
        background-color: #eff7fe;
        color: #007d9e;
        line-height: 29px;
        margin-top: 23px;
        margin-left: 30px;

        .select-current-price-values {
          cursor: default;
          position: absolute;          
          width: 250px;
          z-index: 9999;

          .hint-tiangle {
            height: 12px;
            background-position: 50px bottom;
            background-repeat: no-repeat;
            background-image: url('/static/img/Triangle_currency.png');
          }

          .select-current-price-values-content {
            border-radius: 6px;
            background-color: #eff7fe;
            padding-top: 32px;
            padding-left: 29px;
            padding-right: 29px;
            padding-bottom: 24px;
          }
          
        }
        .select-current-currency {
          cursor: pointer;
          float: left;
          padding-left: 15px;
          padding-right: 15px;
          font-size: 15px;
          font-weight: 600;
          text-align: center;
          color: #005369;
        }
        .select-current-currency:hover {
          color: #0a9bbe;
        }
      }
    }
    .hc-col-coins {
      width: 85%;
      float: left;
      padding-top: 18px;
      padding-left: 20px;
      padding-right: 20px;

      @media screen and (max-width: 767px){
        padding-top: 20px;
        padding-bottom: 20px;
        width: 100%;
      }

      .hc-col-coin {
        display: table;
        vertical-align: middle;
        float: left;
        width: (100 / 7) + 0%;

        @media screen and (max-width: 1200px) {
          width: (100 / 6) + 0%;
        }
        @media screen and (max-width: 992px){
          width: (100 / 4) + 0%;
        }
        @media screen and (max-width: 768px){
          width: (100 / 3) + 0%;
        }
        @media screen and (max-width: 576px){
          width: (100 / 2) + 0%;
        }
        @media screen and (max-width: 360px){
          width: (100 / 1) + 0%;
        }

        .header-currency-coin {
          font-size: 13px;
          font-weight: 300;
          text-align: left;
          height: 40px;
          display: table-cell;
          vertical-align: middle;

          .header-currency-coin-value {
            color: #6f6f6f;
          }

          .price-value-grow {
            color: #00bd6a;
          }
          .price-value-fall {
            color: #ef2b04;
          }

          .price-change {
            margin-left: 2px;
            display: inline-block;
            width: 11px;
            height: 10px;
            background-position: center center;
            background-repeat: no-repeat;
          }

          .price-grow {
            background-image: url('/static/img/Triangle_green.png');
          }
          .price-fall {
            background-image: url('/static/img/Triangle_red.png');
          }

          .price-dir0 {
            background-image: none;
          }
          .price-dir1 {
            background-image: url('/static/img/Triangle_green.png');
          }
          .price-dir-1 {
            background-image: url('/static/img/Triangle_red.png');
          }

        }
      }
    }

    
  }
  
  .currency-columns {
    padding-top: 20px;
  }
</style>




