<template>
  <div id="coinsMined" class="mk-panel">
    <div class="mk-panel-header">
      <i class="mk-icon mk-icon-26 mk-icon-coins left"></i>
      <h3 class="mk-panel-title">Coins mined</h3>
      <div class="mk-icon mk-icon-18 mk-icon-question right mk-icon-btn have-hint">
        <div class="hint" style="display:none;">
          <div class="hint-triangle"></div>
          <div class="hint-content">
            <p>See what algo is making more money here.</p>
            <p>For a full version please press        button</p>
            <p>Typically, we take the last 250 shares, calculate their dollar value, and update the graph.</p>
            <p>Please note that this graph just gives an approximation of what you may be earning.</p>
            <p>The real numbers may be a the same or lower for large miners and higher for small miners (for various reasons). If you want to earn more, please ensure to have fast internet connection and  miners that restart quickly.</p>
          </div>
        </div>
      </div>
      <div class="clearifx"></div>
    </div>
    <div class="mk-panel-body">
      <div class="mined-conis-container">
        <div 
          v-for="coin in coins" :key="coin.id"
          class="mined-coins-item">
          {{coin.name}}
        </div>
        <div class="clearfix"></div>
      </div>

      <button class="mkbtn mkbtn-round-large mkbtn-block mkbtn-round-large-outline-primary"
        @click="goto_block_explorer">
        Block Explorer
      </button>
      <button class="mkbtn mkbtn-round-large mkbtn-block mkbtn-round-large-outline-primary"
        @click="goto_wallet_manager">
        Wallet manager
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data: function(){
    return {
      coins : [
        { id: 1, name: "BTC" },
        { id: 2, name: "BCH" },
        { id: 3, name: "DASH" },
        { id: 4, name: "BTC" },
        { id: 5, name: "LTC" },
        { id: 6, name: "ETC" },
        { id: 7, name: "ZCH" },
        { id: 8, name: "LTC" },
        { id: 9, name: "ETH" },
        { id: 10, name: "XRP" },
        { id: 11, name: "NEM" },
        { id: 12, name: "ETH" }
      ]
    }
  },
  methods : {
    goto_block_explorer() {
      this.$router.push({ name : 'BlockExplorer' });
    },
    goto_wallet_manager() {
      this.$router.push({ name : 'WalletManager' });
    }
  }
}
</script>

<style lang="scss">

  .mkbtn-round-large + .mkbtn-round-large {
    margin-top: 15px;
  }
  .mined-conis-container {
    padding-bottom: 16px;
  }

  .mined-coins-item {
    cursor: pointer;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 6px;
    padding-bottom: 6px;
    margin-top: 3px;
    margin-bottom: 3px;
    border-radius: 5px;
    background: transparent;
    text-align: center;
    width: 25%;
    float: left;
    font-size: 16px;
    color: #0f0f0f;
  }

  .mined-coins-item:hover, .mined-coins-item.active {
    color: #ffffff;
    background-color: #0a9bbe;
    box-shadow: 0 10px 10px -8px rgba(0,125,158,0.59);;
  }
</style>



