<template>
  <div class="filter-select filter-select--collapsed" @click="toggle">
    <div class="current-item">
      <input type="text" class="visually-hidden" />
      <span>Filter</span>
    </div>
    <div class="chevron">
      <div class="chevron__icon"></div>
    </div>
    <div class="values">
      <div class="values--item" v-for="item in items" :key="item.id">
        <input type="text" class="visually-hidden" />
        <span>{{item.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props : {
    items : Array
  },
  methods : {
    toggle(e) {
      $(e.currentTarget)
        .toggleClass('filter-select--collapsed')
        .toggleClass('filter-select--expanded');
    }
  }
}
</script>

<style lang="scss">
  @import "@/styles/var.scss";
  @import "../../styles/common.scss";

  .filter-select {
    @include selection-none;
    position: relative;
    width: 128px;
    height: 36px;
    border-radius: 3px;
    border: solid 1px #c8e8f0;
    font-size: 13px;
    cursor: pointer;

    &--collapsed {
      .chevron__icon {
        transform: rotate(0);
        transition: 100ms linear;
      }
      .values {
        display: none;
      }
    }

    &--expanded {
      .chevron__icon {
        transform: rotate(-180deg);
        transition: 100ms linear;
      }
      .values {
        display: block;
      }
    }
    
    .current-item {      
      color: #036f9f;
      padding: 10px 16px 10px 16px;
    }
    .chevron {
      position: absolute;
      top: 0;
      right: 0;
      .chevron__icon {
        position: relative;
        width: 36px;
        height: 36px;

        &::before {        
          content: '';
          position: absolute;
          top: 17px;
          left: 11px;
          //display: block;
          width: 8px;
          height: 2px;
          background-color: #000000;
          transform: rotate(45deg);
        }
        &::after {
          content: '';
          position: absolute;
          top: 17px;
          left: 16px;
          //display: block;
          width: 8px;
          height: 2px;
          background-color: #000000;
          transform: rotate(-45deg);
        }
      }
    }

    .values {
      position: absolute;
      top: 34px;
      left: 0;
      right: 0;
      padding-top: 6px;
      padding-bottom: 6px;
      background-color: #ffffff;
      border: solid 1px #c8e8f0;

      .values--item {
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 12px;
        padding-right: 12px;

        &:hover {
          color: #ffffff;
          background-color: $color-focused;
        }
      }
    }
  }
</style>



