<template>
  <div class="text-combo-select">
    <label>{{label}}</label>
    <input type="text" placeholder="Type here" />
    <div class="combo-select combo-select--closed">
      <div class="combo-select-value" @click="toggleSelect($event)">
        {{current ? current['title'] : ''}}
        <i class="toggle-icon fas fa-angle-down"></i>
      </div>
      <div class="combo-select__items">
        <div class="combo-select__item"
          v-for="item in listItems" :key="item.id">
          {{item.title}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props : {
    label : String,
    listItems : Array
  },
  data : function(){
    return {
      current : {}
    }
  },
  created : function(){
    if (this.listItems.length > 0) {
      this.current = this.listItems[0];
    }
  },
  methods : {
    toggleSelect : function(event) {
      $(event.currentTarget).parent()
        .toggleClass('combo-select--closed')
        .toggleClass('combo-select--opened');
    }
  }
}
</script>

<style lang="scss">

  @import "../../styles/common.scss";

  .text-combo-select {
    position: relative;    
    margin-top: 30px;
    margin-right: 60px;
    @include selection-none;
    z-index: 0;

    label {
      font-size: 12px;
      text-align: left;
      color: #787878;
    }

    input {
      border-top: none;
      border-left: none;
      border-right: none;
      border-bottom: solid 1px #cfe1e5;
      font-size: 16px;
      font-weight: 300;
      margin-top: 10px;
      padding-top: 10px;
      padding-bottom: 13px;
      color: #000000;

      position: relative;
      margin-top: 0;
      display: inline-block;
      width: 100%;
      z-index: 0;
    }
    .combo-select {
      position: absolute;
      padding: 10px;
      right: -70px;
      top: 18px;
      text-align: left;
      width: 80px;
      background-color: #ffffff;
      border: solid 1px #ffffff;
      z-index: 0;

      .combo-select__items {
        padding-top: 10px;
        z-index: 999;
      }

      &.combo-select--opened {
        position: absolute;
        border: solid 1px #0a9bbe;
        z-index: 10!important;
        display: block;

        .combo-select-value {
          .toggle-icon {
            transform: rotate(-180deg);
            transition: 100ms linear all;
          }
        }
      }

      &.combo-select--closed {
        .combo-select__items {
          display: none;          
        }

        .combo-select-value {
          .toggle-icon {
            transform: rotate(0deg);
            transition: 100ms linear all;
          }
        }
      }
      .combo-select-value {
        cursor: pointer;
      }
      .combo-select__item {
        cursor: pointer;
      }
      .combo-select__item + .combo-select__item{
        margin-top: 10px;
      }
    }
  }
</style>



