<template>  
  <div id="auth-container">
    <div class="auth-form-container">
      <img src="static/img/logo-fire.png" 
        srcset="static/img/logo-fire@2x.png 2x, static/img/logo-fire@3x.png 3x"
        class="mk-logo-fire" />
      <h3 class="text-center">Register</h3>
      <div class="form-container">
        <form class="login-form mk-form" action="">
          <div class="form-input">
            <label>Nickname</label>
            <input name="nickname" type="text" v-model="form.login" placeholder="Create your unique nickname" />
          </div>
          <div class="form-input">
            <label>E-mail</label>
            <input name="email" type="email" v-model="form.email" placeholder="Enter your email" />
          </div>

          <password-input title="Password" ref="password" />
          <password-input title="Repeat password" ref="passwordRe" />

          <vue-recaptcha
            ref="recaptcha"
            class="reCaptcha"
            @verify="onVerify"
            @expired="onExpired"
            :sitekey="sharedStore.reCaptchaSiteKey">
          </vue-recaptcha>

          <button type="button" class="mkbtn mkbtn-primary mkbtn-block" @click="registerClicked">Register</button>

        </form>
        <div class="underform-info">
          <p>By clicking Register, I agree to <a href="">Minekamps Terms of Service</a></p>
          <hr class="dashed" />
          <p>Already have an account? <a href="/login">Login</a></p>
        </div>
      </div>
    </div> <!--register-form-container-->        
  </div>
</template>

<script>
  import { WebApi } from '@/webapi';
  import { SharedStore } from '@/shared-store';
  import VueRecaptcha from 'vue-recaptcha';
  import PasswordInput from '../components/elements/PasswordInput';

  export default {
    components: {
      VueRecaptcha,
      PasswordInput
    },
    data() {
      return {
        sharedStore : SharedStore.state,
        form : {
          login : '',
          email : '',          
          password : '',
          passwordRe : '',
          captcha : ''
        }
      }
    },
    methods : {
      showPassword: function($event) {
        $($event.currentTarget).attr({
          src : "/static/img/open-eye.png",
          srcset : "/static/img/open-eye@2x.png 2x, /static/img/open-eye@3x.png 3x",        
        })
        .parent().find('input').attr({ type : "text" });
      },
      hidePassword: function($event){
        $($event.currentTarget).attr({
          src : "/static/img/closed-eye.png",
          srcset : "/static/img/closed-eye@2x.png 2x, /static/img/closed-eye@3x.png 3x",        
        })
        .parent().find('input').attr({ type : "password" });
      },
      onVerify: function (response) {
        this.form.captcha = response;
      },
      onExpired: function () {
        console.log('Expired')
      },
      registerClicked(e) {
        let self = this;

        self.$refs.recaptcha.reset();   
        self.form.password = self.$refs.password.getValue();
        self.form.passwordRe = self.$refs.passwordRe.getValue();

        WebApi.register(self.form,
          function(r){
            self.$refs.recaptcha.reset();
            console.log(r);
          });
      }
    }
  }
</script>

<style lang="scss">
  @import "../styles/account-form.scss";

</style>



