<template>
  <div id="stratum-generator-expanded" class="mk-panel">

    <div class="mk-panel-header">
      <a class="btn-icon" href="" @click="go_back">
        <i class="mk-icon mk-icon-minimize"></i>
      </a>
      <h3 class="mk-panel-title">Stratum generator</h3>
    </div>

    <div class="mk-panel-body">
      <div class="algorythm-container">
        <div class="mk-btn-group clearfix">
          <button type="button" class="active">SHA-256</button>
          <button type="button">Scrypt</button>
          <button type="button">x11</button>
        </div>
      </div>
      <div class="input-group">
        <ul class="mk-tab">
          <li class="active">
            <a href="">Email</a>
          </li>
        </ul>
      </div>

      <div class="mk-row">
        <input type="text" class="mk-panel-input input-max-265" placeholder="Type here" />
        <div class="input-group width-2of3 clearfix">
          <mk-select class="input-max-265" label="Server" :listItems="servers" style="display: inline-block;margin-right:9px;" />
          <div class="label-container">
            <small class="under-input-info">Please choose a server nearest to your farm</small>
          </div>
        </div>
      </div>
      <div class="text-account-details">
        We will email account access details if you don't have an account yet.
      </div>
      <div class="double_dotted_line"></div>
      <div class="input-name">
        <input-with-label label="Name:" />
      </div>

      <div class="mk-row clearfix">
        <div class="col-link clearfix">
          <div class="input-group">
            <radio :name="link-or-installer" label="LINK" checked="checked" />
            <a href="" class="btn-icon">
              <i class="mk-icon mk-icon-question"></i>
            </a>
            <div class="text-content">
              Choose Link to:
              <ul>
                <li>  Mine using Asic</li>
                <li>  Mine using another mining program</li>
              </ul>
            </div>
          </div>
          <div class="input-group">
            <div class="algorythm-container clearfix">
              <div class="mk-btn-group clearfix">
                <button type="button" class="active">SHA-256</button>
                <button type="button">Scrypt</button>
                <button type="button">x11</button>
              </div>
              <div class="btn-group-label-container">
                <small>Choose your algoritm</small>
              </div>
            </div>
            <div class="mk-info">
              <strong>Link:</strong> minekamp.com:836
              <br /><br />
              <strong>Login:</strong> abdfkj47hf
              <br /><br />
              <strong>Pass:</strong> x=SHA256 d=AUTO L=NONE
            </div>
          </div>
        </div> <!-- col link -->

        <div class="col-installer clearfix">
          <div class="input-group">
            <radio :name="link-or-installer" label="INSTALLER" />
            <a href="" class="btn-icon">
              <i class="mk-icon mk-icon-question"></i>
            </a>
            <div class="text-content">
              Choose "Installer" to:
              <ul>
                <li>  Mine on your computer</li>
                <li>  Mine on a video card farm</li>
              </ul>
            </div>
          </div>

          <div class="input-group">
            <div class="algorythm-container clearfix">
              <div class="mk-btn-group has-four clearfix">
                <button type="button" class="active">SHA-256</button>
                <button type="button">Scrypt</button>
                <button type="button">x11</button>
                <button type="button">Auto Select</button>
              </div>
              <div class="btn-group-label-container">
                <small>Choose your algoritm</small>
              </div>          
            </div>
          </div>

          <div class="input-group">
            <div class="algorythm-container">
              <div class="mk-btn-group mk-btn-group-sm clearfix">
                <button type="button" class="active">
                  <i class="mk-icon mk-icon-windows"></i>
                  Windows
                </button>
                <button type="button">
                  <i class="mk-icon mk-icon-linux"></i>
                  Linux
                </button>
                <button type="button">
                  <i class="mk-icon mk-icon-apple"></i>
                  Mac
                </button>
              </div>
            </div>
          </div>
          <div class="clearfix"></div>
          <div class="select-videocard">
            <mk-select label="Video card" :listItems="videoCards" />
          </div>
        </div> <!--col-installer-->

      </div>

      <button id="btn-generate" class="mkbtn mkbtn-round-large mkbtn-primary">
        Generate
        </button>

    </div>
  </div>
</template>

<script>

import MkSelect from '@/components/elements/MkSelect';
import InputWithLabel from '@/components/elements/InputWithLabel';
import Radio from '@/components/elements/Radio';

export default {
  components : {
    MkSelect,
    InputWithLabel,
    Radio
  },
  data: function(){
    return {
      servers : [
        { id: 0, name: 'AUTO' },
        { id: 1, name: 'New York' },
        { id: 2, name: 'China' }
      ],
      videoCards : [
        { id: 0, name: 'Nvidia', icon: 'mk-icon-nvidia' },
        { id: 1, name: 'AMD', icon: 'mk-icon-amd'},
        { id: 2, name: 'Any', icon: 'mk-icon-any' }
      ]
    }
  },
  methods : {
    go_back : function(){
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/modules/StratumGeneratorExpanded.scss";
</style>



