<template>
  <div class="mk-checkbox">
    <label>
      <input ref="hiddenValue" type="checkbox" value="0" @change="changed($event)" :checked="checked" />
      <span class="mk-checkbox-icon"></span>
      {{label}}
    </label>
  </div>
</template>

<script>
export default {
  props : {    
    label : String,
    labelFromLeft : Boolean,
    onChange : Function,
    checked : Boolean
  },
  methods : {
    changed(e) {
      if (this.onChange) {
        this.onChange(e.target.checked);
      }
    }
    // isChecked : function() {
    //   return this.$refs.hiddenValue.value === "1";
    // },
    // _setChecked: function(value){
    //   if (value === true) {
    //     this.$refs.hiddenValue.value = "1";
    //     $(this.$refs.icon).addClass('checked');
    //   } else {
    //     this.$refs.hiddenValue.value = "0";
    //     $(this.$refs.icon).removeClass('checked');
    //   }
    // },
    // _onClicked : function(e){
    //   this._setChecked(!this.isChecked());
    // }
  }
}
</script>

<style lang="scss">
.mk-checkbox {
  position: relative;
  display: inline-block;
  font-size: 12px;
  cursor: pointer;
  
  input[type="checkbox"] {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  input[type='checkbox']:focus + .mk-checkbox-icon {
    border: solid 1px #0a9bbe; 
  }
  input[type='checkbox']:checked + .mk-checkbox-icon {
    background-image: url(/static/img/checked.png);
  }

  label {
    display: inline-block;
    cursor: pointer;
    margin: 0!important;
    //padding: 0!important;
    padding-left: 30px;
    line-height: 22px!important;
  }
  .mk-checkbox-icon {
    position: absolute;
    left: 0;
    //display: inline-block;
    width: 22px;
    height: 22px;
    border-radius: 3px;
    background-color: #ffffff;
    border: solid 1px #c8e8f0;
    background-position: center;
    background-repeat: no-repeat;
  }

 }
</style>



