<template>
  <div class="mk-panel">
    <div class="mk-panel-header">
      <div class="mk-panel-title">FAQ</div>
    </div>
    <div class="mk-panel-body">
      <div class="faq-container clearfix">
        <div class="faq-item collapsed" v-for="item in questions" :key="item.id">
          <div class="question">
            <div class="question-avatar"></div>
            <div class="question-balloon">
              <div class="question-expand-button" @click.prevent="on_expand_clicked"></div>
              <h3>{{item.text}}</h3>
              <div class="question-balloon-tail"></div>
            </div>
          </div>
          <div class="answers">
            <div class="answer-item" v-for="answer in item.answers" :key="answer.id">
              <div class="answer-balloon">
                <div class="answer-text" v-html="answer.text"></div>                
                <div class="answer-balloon-tail"></div>
              </div>
              <div class="answer-avatar"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      questions : [
        {
          id: 10,
          text : "What is MineKamp?",
          answers : [
            {
              id : 11,
              text: "<p>MineKamp is a fintech company that is working to create the social and technological infrastructure needed to make effective decisions under the volatile conditions of the new economy.</p><p>By combining a large number of diverse financial analysts and a set of machine learning models into a single system, we are developing a Hybrid Intelligence infrastructure for the efficient management of investors' capital in traditional financial and cryptomarkets.</p>"
            },
            {
              id : 12,
              text: "<a href=\"url\">video</a>"
            }
          ]
        },
        {
          id: 20,
          text: "Are there any finished MineKamp products?",
          answers : [
            {
              id : 21,
              text : ""
            }
          ]
        },
        {
          id: 30,
          text: "Why does MineKamp need issuance of our own infrastructure tokens?",
          answers : [
            {
              id : 31,
              text : ""
            }
          ]
        }
      ]
    }
  },
  methods: {
    on_expand_clicked(e) {
      $(e.target).parent().parent().parent()
        .toggleClass('collapsed')
        .toggleClass('expanded');

    }
  }
}
</script>

<style lang="scss">
  @import "../styles/components/faq.scss";
</style>



