const _base_uri                     = 'https://minekamp.com/api/';

let uri_resigeter                   = 'https://minekamp.com/api/cabinet/register';
let uri_login                       = 'https://minekamp.com/api/cabinet/login';
let uri_restore_password            = 'https://minekamp.com/api/cabinet/restore_password';
let uri_change_email                = 'https://minekamp.com/api/cabinet/change_email';
let uri_change_password             = 'https://minekamp.com/api/cabinet/change_password';
let uri_change_password_reset_email = 'https://minekamp.com/api/cabinet/change_password_reset_email';

export default {

  /* register */
  register({ login, email, password, passwordRe, captcha }, callback) {
    let prm = {
      'userNick' : login,
      'userEmail' : email,
      'userPassword' : password,
      'repeatPassword' : passwordRe,
      'g-recaptcha-response' : captcha
    };
    $.post(uri_resigeter, prm, function(r){
      callback(r);
    });
  },

  /* login */
  login({ login, password, captcha }, callback) {
    let prm = {
      'userEmail' : login,
      'userPassword' : password,
      'g-recaptcha-response' : captcha
    };
    $.post(uri_login, prm, function(r){
      callback(r);
    });
  },

  hashrate(name, callback) {
    //$.get('http://localhost:3000/api/hashrates_' + name, {}, function(r){
    $.get('/static/data/hashrates_' + name + '.json', {}, function(r){
      callback(r);
    });
  },

  virtualWallets(callback) {
    $.get('/static/data/virtual_wallets.json', {}, function(r){
      callback(r);
    })
  }
}


