import moment from 'moment';

var KEY_IS_AUTH = 'mkk-is-auth';
var KEY_LOGIN = 'mkk-login';
var KEY_TOKEN = 'mkk-token';

export const AuthStore = {

  state: {
    isAuthenticated : false,
    login : ''
  },

  setToken(token, expires_in) {
    let expires = moment().add(expires_in, 's');

    let auth = {
      token : token,
      refresh_token : null,
      expires : expires
    };
    localStorage[KEY_TOKEN] = JSON.stringify(auth);
  },

  getToken() {
    let auth_str = localStorage[KEY_TOKEN];
    if (typeof auth_str !== 'undefined' && auth_str !== null && auth_str !== '') {
      let auth = JSON.parse(auth_str);
      if (auth && auth.token && auth.expires) {
        if (moment().isBefore(auth.expires)) {
          return auth.token;
        }
      }
    }
    alert('Token is expired');
    return null;
  },

  clearToken() {
    localStorage[KEY_TOKEN] = null;
  },

  isAuthenticated() {
    let auth_str = localStorage[KEY_TOKEN];
    if (typeof auth_str !== 'undefined' && auth_str !== null && auth_str !== '') {
      let auth = JSON.parse(auth_str);
      if (auth && auth.token && auth.expires) {
        return moment().isBefore(auth.expires);
      }
    }
    return false;    
  },

  setLogin(value) {
    localStorage.setItem(KEY_LOGIN, value);
  },

  login(login, password) {
    this.setLogin(login);
    this.setAuthenticated(true);
  },

  logout() {
    this.setLogin(null);
    this.clearToken();
  }
}


