<template>
  <div id="wallet-manager">
    <div class="mk-panel">
      <div class="mk-panel-header">
        <h3 class="mk-panel-title">
          Wallet Manager
          <hint-question :text="hints.walletManager" />
        </h3>        
      </div>
      <div class="mk-panel-body">
        <label class="small-label">Choose coin</label>
        <div class="coins-for-choose-container">
          <div class="coin-item" 
            v-for="(coin, index) in coins" 
            :key="coin.id">
            <div class="coin-item__button" :class="{ 'active' : index === 3 }">
              <i class="coin-item__button-icon" :class="'coin-item__button-icon--' + coin.alias"></i>
              {{coin.short}}
            </div>
            <div class="coin-item-title">{{coin.title}}</div>
          </div>
        </div>

        <button id="btn-generate" class="btn-default-outline active" @click="show_generate">Generate</button>
        <button id="btn-manage" class="btn-default-outline" @click="show_manage">Manage</button>

        <div class="generate-container">
          <div class="info-group clearfix">
            <div class="input-with-label">
              <label class="small-label">Wallet (share)</label>
              <input class="sigleline-info" value="qbocdxQ1348Bz" />
            </div>
            <div class="button-with-right-text">
              <button id="btn-new-wallet" class="mkbtn mkbtn-warning mkbtn-small">
                <i class="mkbtn-icon mkbtn-icon--refresh loading-spin"></i>
              </button>
              <label for="btn-new-wallet">New wallet</label>
            </div>
          </div>

          <div class="info-group clearfix">
            <div class="input-with-label">
              <label class="small-label">Public key (share)</label>
              <input class="sigleline-info" value="axlyZQMMNSXPT" />
            </div>
          </div>

          <div class="info-group">
            <div class="info-hint">Please input your private key and click Validate to manage your wallet</div>
            <label class="small-label">Private key</label>
            <div class="input-with-control-links">
              <input id="generate-input-private-key" type="password" class="bordered-input bordered-input-small" />
              <div class="links">
                <a href="" @click.prevent="show_private_key" data-target="#generate-input-private-key">Show</a> / 
                <a href="" @click.prevent="hide_private_key" data-target="#generate-input-private-key">Hide</a>
              </div>
            </div>
          </div>

          <div class="info-group warning-store-info">
            <p>We dont store this information.</p>
            <p><strong>Save your wallet & private key to a safe location!</strong></p>
          </div>

          <div class="info-group">
            <h4>Your public wallet address</h4>
            <div class="qrCode">
              <qr-code error-level="H" size="222" text="www.minekamp.com: 9b02e8b6-e5d9-4eae-bf13-26d7e824ddc5" />
            </div>
          </div>
        </div> <!-- generate-container -->

        <div class="manage-container">
          <div class="info-group">
            <div class="info-hint">Please input your private key and click Validate to manage your wallet</div>
          </div>
          <div class="mk-row">
            <div class="info-group clearfix col-t-min-1of2">              
              <div class="input-with-label">
              <label class="small-label">Private key</label>
              <div class="input-with-control-links">
                <input id="manage-input-private-key" type="password" class="bordered-input bordered-input-small" />
                <div class="links">
                  <a href="" @click.prevent="show_private_key" data-target="#manage-input-private-key">Show</a> / 
                  <a href="" @click.prevent="hide_private_key" data-target="#manage-input-private-key">Hide</a>
                </div>
              </div>
              </div>
            </div>
            <div class="info-group clearfix col-t-min-1of2">
              <div class="input-with-label">
                <label class="small-label">Public key (share)</label>
                <input type="text" class="sigleline-info" value="axlyZQMMNSXPT" />
              </div>
            </div>
          </div>
          <div class="mk-row">
            <div class="info-group col-t-min-1of2">
              <label class="small-label">Balance</label>
              <div class="balance-separator"></div>
              <span class="balance-value">0.12386</span>
            </div>
            <div class="info-group col-t-min-1of2">
              <div class="input-with-label">
                <label class="small-label">Wallet (share)</label>
                <input type="text" class="sigleline-info" value="qbocdxQ1348Bz" />
              </div>
            </div>
          </div>

          <button id="btn-validate" class="mkbtn mkbtn-success mkbtn-with-inline-icon">
            Validate&nbsp; <i class="mkbtn-inline-icon mkbtn-inline-icon--success"></i>
          </button>

          <div class="info-group">
            <div class="double_dotted_line"></div>
          </div>
          <div class="info-group">
            <div class="input-with-label">
              <label class="small-label">Address</label>            
              <input type="text" class="sigleline-info" />
            </div>
          </div>
          <div class="info-group clearfix">
            <div class="input-with-label">
              <label class="small-label">
                Transaction fee
                <hint-question :text="hints.transactionFee" />
              </label>
              <input type="text" class="sigleline-info" placeholder="Leave blank for auto adjustment" />
            </div>
          </div>
          <div class="info-group clearfix">
            <div class="manage-amount">
              <label class="small-label">Amount</label>
              <input type="text" class="sigleline-info" />
            </div>
            <div class="manage-donation">
              <label class="small-label">Donation %</label>
              <input type="text" class="sigleline-info" value="1" />
            </div>
          </div>
          <div class="info-group">
            <button class="mkbtn mkbtn-round-large mkbtn-block mkbtn-primary">Send</button>
          </div>
        </div>
      </div>
    </div> <!-- mk-panel -->
  </div>
</template>

<script>
  import HintQuestion from '@/components/elements/HintQuestion';
  import VueQRCodeComponent from 'vue-qrcode-component';

  export default {
    components : {
      HintQuestion,
      'qr-code' : VueQRCodeComponent
    },
    data() {
      return {
        hints : {
          walletManager : '<p>Hint for wallet manager</p>',
          transactionFee : '<p>Hint for transaction fee</p>'
        },
        coins : [ {
            id : 1,
            alias : 'btc',
            short : 'BTC',
            title : 'Bitcoin'
          }, {
            id : 2,
            alias : 'bch',
            short : 'BCH',
            title : 'Bitcoin Cash'
          }, {
            id : 3,
            alias : 'dash',
            short : 'DASH',
            title : 'Dash'
          }, {
            id : 4,
            alias : 'etc',
            short : 'ETC',
            title : 'Ethereum Classic'
          }, {
            id : 5,
            alias : 'neo',
            short : 'NEO',
            title : 'Neo'
          }, {
            id : 6,
            alias : 'ltc',
            short : 'LTC',
            title : 'Litecoin'
          }, {
            id : 7,
            alias : 'eth',
            short : 'ETH',
            title : 'Ethereum'
          }
        ]
      }
    },
    methods : {
      show_private_key(e) {
        e.stopPropagation();
        e.preventDefault();
        $($(e.currentTarget).data('target')).attr({ type: 'text' });
      },
      hide_private_key(e) {
        e.stopPropagation();
        e.preventDefault();
        $($(e.currentTarget).data('target')).attr({ type: 'password' });
      },
      show_generate(e) {

      },
      show_manage(e) {

      },
      refresh_test(e) {

      }
    }
  }
</script>

<style lang="scss">
  @import "@/styles/var.scss";

  .small-label {
    position: relative;
    display: inline-block;
    font-size: 12px;
    font-weight: 600;
    color: #000000;
    padding-bottom: 16px;

    .mk-panel-header__question {
      top: -8px;
    }
  }
  @import "@/styles/modules/WalletManager.scss";

  #generate-input-private-key, #manage-input-private-key {
    @media (min-width: $tablet-min-width) {
      width: 205px;
      min-width: 205px;
    }
  }
</style>



