<template>
  <div id="auth-container">
    <div class="auth-form-container">
      <img src="static/img/logo-fire.png" 
        srcset="static/img/logo-fire@2x.png 2x, static/img/logo-fire@3x.png 3x"
        class="mk-logo-fire" />
      <h3 class="text-center">Login</h3>
      <form class="login-form mk-form" action="" autocomplete="on" v-if="loginStep === 1">
        <div class="form-input">
          <label>E-mail</label>
          <input name="email" type="email" v-model="form.login" placeholder="Enter your email" autocomplete="on" />
        </div>              
        <password-input ref="passwordInput" title="Password" />            
        <div class="form-input">
          <div style="height: 22px;">
            <checkbox label="Remember me" title="Remember me - for a week, default session live time: 30 minutes" />
            <a class="forgot-password" href="/forgot_password">Forgot password?</a>
            <div class="clearfix"></div>
          </div>
        </div>

        <vue-recaptcha
          ref="recaptcha"
          class="reCaptcha"
          @verify="onVerify"
          @expired="onExpired"
          :sitekey="sharedStore.reCaptchaSiteKey">
        </vue-recaptcha>

        <button type="button" class="mkbtn mkbtn-primary mkbtn-block" @click.prevent="loginClicked">Login</button>
      </form>

      <!-- 2fa form-->
      <form action="" class="login-form mk-form" v-if="loginStep === 2">
      
        <div class="form-input">
          <label>You set 2FA Auth, please check the reCaptcha below and enter a token provided in Google Authentificator app:</label>
        </div>
        <div class="form-input" v-if="fa2captcha">
          <input type="text" v-model="googleAuthCode" placeholder="Enter the Google Authenticator code" autocomplete="off" />
        </div>

        <vue-recaptcha
          ref="reCaptcha2fa"
          class="reCaptcha"
          @verify="on_2fa_verify"
          @expired="on_2fa_expired"
          :sitekey="sharedStore.reCaptchaSiteKey">
        </vue-recaptcha>

        <button type="button" class="mkbtn mkbtn-primary mkbtn-block" @click.prevent="loginConfirm">Login</button>        
      </form>
      <!-- end 2fa -->

      <div class="underform-info">
        <p>By clicking Login, I agree to <a href="">Minekamps Terms of Service</a></p>
        <hr class="dashed" />
        <p>Don't have an account? <router-link :to="{ name : 'Register' }">Register</router-link></p>
      </div>
    </div>
  </div>
</template>

<script>
  import { WebApi } from '@/webapi';
  import { AuthStore } from '@/auth-store';
  import { SharedStore } from '@/shared-store';
  import VueRecaptcha from 'vue-recaptcha';
  import PasswordInput from '../components/elements/PasswordInput';
  import Checkbox from '../components/elements/Checkbox';

  export default {
    components: {
      VueRecaptcha,
      PasswordInput,
      Checkbox
    },
    data() {
      return {
        loginStep: 1,
        googleAuthCode : null,

        login : null,
        sharedStore : SharedStore.state,
        fa2captcha : null,

        form : {
          login: null,
          password : null,
          captcha : null
        }
      }
    },
    methods : {
      loginClicked(e) {
        let self = this;
        
        self.form.password = this.$refs.passwordInput.getValue();

        WebApi.login(self.form, function(r){
          self.$refs.recaptcha.reset();          
          if (r.isSuccess) {
            if (r.data.need2fa) {
              self.googleAuthCode = null;
              self.loginStep = 2;
            } else {
              AuthStore.setToken(r.data.token.access_token, r.data.token.expires_in);
              //self.$router.replace('/');
              //location.href = "/";
            }
          }
        })
      },
      loginConfirm() {
        let self = this;
        self.$refs.reCaptcha2fa.reset();
        let prm = {
          login : self.form.login, 
          password : self.form.password,
          rememberMe : false, 
          captcha : self.fa2captcha, 
          googleCode: self.googleAuthCode
        };
        WebApi.login2fa(prm, 
          function(r){
            if (!r.isSuccess) {
              alert(r.message);
            } else {
              AuthStore.setToken(r.data.token.access_token, r.data.token.expires_in);
              self.$store.commit('UserSettings/set', { fa2 : r.data.settings.FA2, forbidTor : r.data.settings.ForbidTOR });
              location.href = "/";
            }
          });
      },
      onVerify: function (response) {
        this.form.captcha = response;
      },
      onExpired: function () {
        this.form.captcha = null;
      },
      on_2fa_verify(r) {
        this.fa2captcha = r;
      },
      on_2fa_expired() {
        this.fa2captcha = null;
      }
    }
  }
</script>

<style lang="scss">
  @import "../styles/account-form.scss";
  .forgot-password {
    float: right;
    padding-top: 5px;
  }
</style>


