 <template>
  <div class="mk-select mk-select-values--collapsed" @click="toggle">
    <label>{{label}}</label>
    <div class="mk-select-value">
      <input type="text" :value="current.id" class="visually-hidden" />
      {{current.name}}
      <i class="mk-select-chevron fas fa-angle-down"></i>
    </div>
    <div class="mk-select-values">
      <div class="mk-select-values__container">
        <div 
          v-for="item in listItems" :key="item.id"
          class="mk-select-item"
          @click="select_item(item)">
          <i v-if="item.icon" class="mk-select-item__icon" :class="item.icon"></i>
          <input type="text" :value="item.id" class="visually-hidden" />
          {{item.name}}
        </div>
      </div>
    </div>
  </div>
 </template>
 
 <script>
 export default {
   props : {
     label : String,
     listItems : Array
   },
   data : function() {
     return {
        current : {}
     }
   },
   created: function(){
     if (this.listItems.length > 0) {
       this.current = this.listItems[0];
     }
   },
   methods : {
    toggle: function(e){
      $(e.currentTarget)
      .toggleClass('mk-select-values--collapsed')
      .toggleClass('mk-select-values--expanded');
    },
    select_item : function(item){
      this.current = item;
    }
   }
 }
</script>
 
<style lang="scss">

@import "../../styles/common.scss";

.mk-select {
  @include selection-none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: solid 1px #cfe1e5;
  width: 100%;
  font-size: 16px;
  font-weight: 300;
  //margin-top: 10px;
  //padding-top: 10px;
  padding-bottom: 14px;
  margin-bottom: 13px;
  color: #000000;
  position: relative;
  
  label {
    font-size: 12px;
    text-align: left;
    color: #787878;
    margin-bottom: 8px;
    display: inline-block;
  }
  .mk-select-value {
    position: relative;
    cursor: pointer;
    font-weight: 400;

    i {
      position: absolute;
      right: 0;
      top: 4px;
      color: #0f0f0f;
    }
  }

  &.mk-select-values--collapsed {
    .mk-select-chevron {
      transform: rotate(0deg);
      transition: 100ms linear all;
    }
    .mk-select-values {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.1s ease-out;
    }
  }
  &.mk-select-values--expanded {
    .mk-select-chevron {
      transform: rotate(180deg);
      transition: 100ms linear all;
    }
    .mk-select-values {
      max-height: 1000px;
      transition: max-height 0.1s ease-in;
    }
  }

  .mk-select-values {
    position: absolute;
    left: 0;
    top: 50px;
    width: 100%;
    
    cursor: pointer;

    .mk-select-values__container {
      background-color: #ffffff;
      border: solid 1px #cfe1e5;
      padding-top: 8px;
      padding-bottom: 8px;
    }

    .mk-select-item {
      font-size: 15px;
      padding: 8px 16px 8px 16px;

      &:hover {
        color: #036f9f;
      }

      .mk-select-item__icon {
        display: inline-block;
        width: 16px;
        height: 16px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }
}
</style>
 


