<template>
  <div class="mk-radio">
    <label>
      <input type="radio" class="visually-hidden" :name="name" />
      <i class="mk-radio-icon"></i>
      {{label}}
    </label>
  </div>
</template>

<script>
export default {
  props : {
    name: String,
    label : String
  }
}
</script>

<style lang="scss">
  .mk-radio {
    position: relative;
    display: inline-block;
    font-size: 12px;
    height: 19px;
    line-height: 19px;
    cursor: pointer;

    label {
      font-size: 16px;
      font-weight: bold;
      line-height: 1;
      text-align: left;
      color: #0f0f0f;
      padding-left: 28px;
    }

    &.mk-radio-sm {
      label {
        font-size: 12px;
      }
    }

    .mk-radio-icon {
      position: absolute;
      left: 0;
      top: 0;
      width: 19px;
      height: 19px;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url(/static/img/chek-box.svg);
    }

    input[type='radio']:checked + .mk-radio-icon {
      background-image: url(/static/img/chek-box-active.svg);
    }
  }
</style>



