<template>
  <div id="personal-monitoring">
    <div class="mk-panel">
      <div class="mk-panel-header">
        <div class="buttons-group-tab-container">
          <buttons-group-tab :buttons="tabs" :onChange="on_tab_changed" />
        </div>
        <div class="clearfix"></div>
      </div>
      <div class="mk-panel-body">

      </div>
    </div>
  </div>
</template>

<script>
import ButtonsGroupTab from '@/components/elements/ButtonsGroupTab'
export default {
  components : {
    ButtonsGroupTab
  },
  data() {
    return {
      tabs : [
        { id: 1, caption: 'List by plans' },
        { id: 2, caption: 'List by miners' }
      ]
    }
  },
  methods : {
    on_tab_changed(button) {
      console.log('on_tab_changed:');
      console.log(button);
    }
  }
}
</script>

<style lang="scss">
  .buttons-group-tab-container {
    float: right;
  }
</style>



