<template>
  <div class="nav-main">
    <div class="nav-main__header">
      <div class="container clearfix">
        <div class="nav-main__mobile-container clearfix">
          <div class="nav-main__logo">
            <a href="/">
              <img src="/static/img/logo.png"
                srcset="/static/img/logo@2x.png 2x, img/logo@3x.png 3x" alt="" />
            </a>
          </div>

          <div class="language language--closed">
            <div class="language__current" @click="onCurrentLanguageClick">
              {{language.caption}}
              <i class="language__collapse-icon"></i>
            </div>
            <div class="language__available-list">
              <div class="language_available-item"
                v-for="lang in languages" :key="lang.id"
                @click="onAvailableLanguageClick(lang)">              
                {{lang.title}}
              </div>
            </div>
          </div>
        </div>

        <button 
          @click="toggleMenu()"
          class="nav-toggler nav-toggler--closed" type="button"
          :class="{ 'has-auth' : isLoggedIn }">
          <i></i>
          <i></i>
          <i></i>
        </button>

        <div class="nav-main__personal-menu-container" 
          v-if="isLoggedIn">
          <div class="nav-main_personal-menu clearfix">
            <div class="nav-main_personal-menu__item nav-main_personal-menu--miner">
              <router-link :to='{ name: "BlockExplorer" }' tag="div">
                <a></a>
                <div class="bottom-selector"></div>
              </router-link>
              
            </div>
            <div class="nav-main_personal-menu__item nav-main_personal-menu--monitor">
              <router-link :to='{ name: "Monitoring" }' tag="div">
                <a></a>
                <div class="bottom-selector"></div>
              </router-link>              
            </div>
            <div class="nav-main_personal-menu__item nav-main_personal-menu--settings">
              <router-link :to='{ name: "Settings" }' tag="div">
                <a></a>
                <div class="bottom-selector"></div>
              </router-link>
            </div>
            <div class="nav-main_personal-menu__item nav-main_personal-menu--support">
              <router-link :to='{ name: "Support" }' tag="div">
                <a></a>
                <div class="bottom-selector"></div>
              </router-link>
            </div>
          </div>
        </div>
      
        <div class="nav-main__menu-container nav-main__menu-container--collapsed"
          :class="{ 'has-auth' : isLoggedIn }">

          <div class="container">
            <ul class="nav-main__menu">
              <li>
                <router-link :to="{ name: 'News' }">News</router-link>
              </li>
              <li>
                <router-link :to="{ name: 'Forum' }">Chat/Forum</router-link>
              </li>
              <li>
                <router-link :to="{ name: 'Faq' }">FAQ</router-link>
              </li>
              <li>
                <router-link :to="{ name: 'Token' }">Token</router-link>
              </li>
              <li>
                <router-link :to="{ name: 'Contacts' }">Contacts</router-link>
              </li>
            </ul>

          </div> <!--container-->
        </div> <!-- nav-main__menu-container -->

        <div class="nav-main__menu-login-container"
          :class="{ 'has-auth' : isLoggedIn }">
          <div class="wrapper">
            <ul class="nav-main__menu-login"
              v-if="!isLoggedIn">
              <li>
                <router-link :to="{ name: 'Register' }">Register</router-link>
              </li>
              <li>
                <router-link :to="{ name: 'Login' }">Login</router-link>
              </li>        
            </ul>

            <ul v-if="isLoggedIn">
              <li>
                <a href="" @click.prevent="logout">
                  Logout
                </a>
              </li>
            </ul>
          </div>
        </div>

      </div> <!-- container -->
      
    </div>
  </div>
</template>

<script>

import { AuthStore } from '@/auth-store';
import { mapGetters } from 'vuex';

export default {
  watch: {
    '$route' () {
      this.closeMenu();
    }
  },
  data: function() {
    return {
      languages : [
        { id: 1, title: "English", caption: "Eng" }
      ],
      language : {},
      authState : AuthStore.state
    }
  },
  computed : {
    ...mapGetters('Authorization', {
       isLoggedIn : 'isLoggedIn'
    })
  },
  created() {
    this.language = this.languages[0];
  },
  methods: {
    onCurrentLanguageClick : function(e){
      $('.language')
        .toggleClass('language--opened')
        .toggleClass('language--closed');
    },
    onAvailableLanguageClick: function(lang){
      this.language = lang;
      $('.language')
        .toggleClass('language--opened')
        .toggleClass('language--closed');
    },

    closeMenu() {
      $('.nav-toggler')
        .addClass('nav-toggler--closed')
        .removeClass('nav-toggler--opened');

      $('.nav-main__menu-container')
        .removeClass('nav-main__menu-container--expanded')
        .addClass('nav-main__menu-container--collapsed');
    },
    toggleMenu: function(){
      $('.nav-toggler')
        .toggleClass('nav-toggler--closed')
        .toggleClass('nav-toggler--opened');

      $('.nav-main__menu-container')
        .toggleClass('nav-main__menu-container--expanded')
        .toggleClass('nav-main__menu-container--collapsed');
    },
    logout() {
      AuthStore.logout();
      location.href = "/";
    }
  }
}
</script>

<style lang="scss">  
  @import "@/styles/menu/NavMain.scss";
  @import "@/styles/menu/NavMainPersonalMenu.scss";
</style>



