<template>
  <div id="liveStats" class="mk-panel">
    <div class="mk-panel-header">
      <i class="mk-icon mk-icon-26 mk-icon-stats left"></i>
      <h3 class="mk-panel-title">Live stats</h3>
      <div class="mk-icon mk-icon-18 mk-icon-question right mk-icon-btn have-hint">
        <div class="hint" style="display:none;">
          <div class="hint-triangle"></div>
          <div class="hint-content">
            <p>See what algo is making more money here.</p>
            <p>For a full version please press        button</p>
            <p>Typically, we take the last 250 shares, calculate their dollar value, and update the graph.</p>
            <p>Please note that this graph just gives an approximation of what you may be earning.</p>
            <p>The real numbers may be a the same or lower for large miners and higher for small miners (for various reasons). If you want to earn more, please ensure to have fast internet connection and  miners that restart quickly.</p>
          </div>
        </div>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="mk-panel-body">
      <div class="live-status-progress-items">
        <table class="live-status-progress-table">
          <tr>
            <td colspan="2" class="label">
              SHA-256
            </td>
          </tr>
          <tr>
            <td class="progress">
              <div class="progress-background">
                <div class="progress-value" style="width:56%"></div>
              </div>
            </td>
            <td class="value">
                3 
            </td>
          </tr>
        </table>

        <table class="live-status-progress-table">
          <tr>
            <td colspan="2" class="label">
              Scrypt
            </td>
          </tr>
          <tr>
            <td class="progress">
              <div class="progress-background">
                <div class="progress-value" style="width:36%"></div>
              </div>
            </td>
            <td class="value">
                3 
            </td>
          </tr>
        </table>

        <table class="live-status-progress-table">
          <tr>
            <td colspan="2" class="label">
              X11
            </td>
          </tr>
          <tr>
            <td class="progress">
              <div class="progress-background">
                <div class="progress-value" style="width:16%"></div>
              </div>
            </td>
            <td class="value">
                3 
            </td>
          </tr>
        </table>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  data : function(){
    return {

    }
  },
  actions: {
    
  }

}
</script>

<style lang="scss">
  .live-status-progress-items {
    padding-bottom: 20px;
  }
  .live-status-progress-table {
    
    width: 100%;

    td.label {
      font-size: 12px;
      color: #0f0f0f;
      text-align: left;
    }

    td.value {
      text-align: right;
      width: 30px;
      font-size: 12px;
      font-weight: bold;
      color: #0f0f0f;
    }
  }
  .live-status-progress-table + .live-status-progress-table {
    margin-top: 24px;
  }
  .progress-background {
    margin-top: 7px;
    width: 100%;
    height: 12px;
    border-radius: 12px;
    background-color: #ffe5bc;

    .progress-value {
      border-top-left-radius: 12px;
      border-bottom-left-radius: 12px;
      height: 12px;
      background-image: linear-gradient(to bottom, #ff9c00, #ff6200);
      box-shadow: 1px 2px 3px 0 rgba(255, 112, 51, 0.5);
    }
  }
</style>



