import MinekampApi from '../../api/MinekampApi';
import moment from 'moment';

var KEY_TOKEN = 'mkk-token';

const state = {
  
}

const getters = {
  isLoggedIn: () => {
    let auth_str = localStorage[KEY_TOKEN];
    if (typeof auth_str !== 'undefined' && auth_str !== null && auth_str !== '') {
      let auth = JSON.parse(auth_str);
      if (auth && auth.token && auth.expires) {
        return moment().isBefore(auth.expires);
      }
    }
    return false;  
  }
}

const mutations = {
  setToken(state, { token, expires_in }) {
    let expires = moment().add(expires_in, 's');
    let auth = {
      token : token,
      refresh_token : null,
      expires : expires
    };
    localStorage[KEY_TOKEN] = JSON.stringify(auth);
  },
}

const actions = {
  login({ commit }, { login, password, captcha }) {
    MinekampApi.login({ login, password, captcha }, (r) => {
      
    })
  }
}

export default {
  namespaced : true,
  state,
  getters,
  mutations,
  actions
}


