<template>
  <div class="input-with-label">
    <label>{{label}}</label>
    <input type="text" />
  </div>
</template>

<script>
export default {
  props : {
    label : String
  }
}
</script>

<style lang="scss">
  .input-with-label {
    width: 100%;
    height: 40px;
    position: relative;
    border-top: none;
    border-right: none;
    border-bottom: solid 1px #f3f8fc;

    input {
      display: inline-block;
      width: auto;      
      border: none;
      margin: 0;
      padding: 0;
    }
    label {
      display: inline-block;
      font-size: 14px;
      font-weight: 600;
      text-align: left;
      color: #0f0f0f;
    }
  }
</style>



