<template>
  <nav ref="nav" role="navigation">
    <ul class="pagination">
      <li class="arrows previous"
        v-on:click.prevent="prev">
        <a href="?p=">previous</a>
      </li>

      <li v-for="item in pages" 
        :key="item.id"
        :class="{ 'current': currentId === item.id }"
        v-on:click.prevent="changePage(item.id)">
        <a href="" v-if="item.id > 0">{{item.caption}}</a>
        <span v-if="item.id < 0">...</span>
      </li>
      <li class="arrows next"
        v-on:click.prevent="next">
        <a href="?p=">next</a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props : {
    total : Number,
    current: Number
  },
  data () {
    return {
      currentId : Number,
      currentWidth: Number
    }
  },
  created() {
    this.currentId = this.current; 
  },
  mounted() {
    this._handleResize();
    window.addEventListener('resize', this._handleResize);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this._handleResize)
  },
  methods: {
    _handleResize() {
      this.currentWidth = this.$refs.nav.clientWidth;
    },
    _getDelta() {
      if (this.currentWidth <= 380)
        return 0;
      else if (this.currentWidth <= 480)
        return 1;
      else if (this.currentWidth <= 580)
        return 2;
      else if (this.currentWidth <= 680)
        return 3;
      return 4;
    },
    changePage: function(value){
      this.currentId = value;
    },
    prev: function(){
      if (this.currentId > 1)
        this.currentId--;
    },
    next: function(){
      if (this.currentId < this.total)
        this.currentId++;
    }
  },
  computed : {
    pages: function(){

      let current = this.currentId;
      let last = this.total;
      let delta = this._getDelta();
      let left = current - delta;
      let right = current + delta + 1;

      let range = [];
      let result = [];

      for(let i = 1; i <= this.total; i++) {
        if (delta > 0) {
          if (i == 1 || i == last || i >= left && i < right) {
            range.push({
              id: i,
              url: '?p=' + i,
              caption: i.toString()
            });
          }
        } else {
          if (i >= left && i < right) {
            range.push({
              id: i,
              url: '?p=' + i,
              caption: i.toString()
            });
          }
        }        
      }
      let l = 0;

      for(let i = 0; i < range.length; i++) {
        if (delta > 0) {
          if (l > 0) {
            if (range[i].id - l == 2) {
                result.push({ id: -i, caption: '...' });
            } else if (range[i].id - l != 1) {
                result.push({ id: -i, caption: '...' });
            }
          }
        }
        result.push(range[i]);
        l = range[i].id;
      }

      return result;
    }
  }
}
</script>

<style lang="scss">
  ul.pagination {
    list-style: none;

    li {
      display: inline-block;
      //height: 30px;

      a, span {
        text-decoration: none;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 1.2px;
        color: #000000!important;
        text-align: center;
        padding: 4px 7px;
        line-height: 1;
      }
      

      &.arrows {
        a {
          color: #0a9bbe!important;
          position: relative;

          &::before {
            content: '';
            position: absolute;
            top: 6px;
            display: inline-block;
            width: 18px;
            height: 18px;
            background-position: center;
            background-repeat: no-repeat;
          }
        }

        &.previous {
          a {  
            padding-left: 27px;

            &::before {              
              left: 0;
              background-image: url(/static/img/arrow-prev.svg);
            }
          }
        }

        &.next {
          a {
            padding-right: 27px;

            &::before {
              right: 0;
              background-image: url(/static/img/arrow-next.svg);
            }
          }
        }
      }
      &:not(.arrows) {
        a {
          display: inline-block;
          height: 23px;
        }
        &.current, &:hover {
          
          a {
            background-color: #0a9bbe;
            color: #ffffff!important;
            border-radius: 6px;
            
            color: #fff;
            text-align: center;
            white-space: nowrap;
            vertical-align: middle;
          }
        }
      }

    }
  }
</style>



