<template>
    <div class="mk-panel">
      <div class="mk-panel-body">
        <form class="mk-form">
          <div class="form-input">

            <Checkbox label="Two-factor authorization" :onChange="twoFactorChanged" :checked="fa2" />
            <div class="clearfix"></div>

            <div class="two-factor-cunfirm" v-if="twoFactorQr">
              <span>For use two-factor authorization please install Google Authenticator app on you phone and scan QR to setup 2FA access to the site</span>
              <div class="app-links">
                <a href="https://play.google.com/store/apps/details?id=com.google.android.apps.authenticator2&hl=en" target="_blank">
                  <img src="/static/img/google-play.png" />
                </a>
                <a href="https://itunes.apple.com/us/app/google-authenticator/id388497605?mt=8" target="_blank">
                  <img src="/static/img/app-store.png" />
                </a>
              </div>
              <img :src="twoFactorQr" :style="{ width: getWidthForCm(4) + 'px' }" />

              <div class="form-input">
                <input name="googleAuthCode" v-model="googleAuthCode" type="text" placeholder="Enter the Google Authenticator code" autocomplete="on" />
              </div> 
              
              <VueRecaptcha
                ref="reCaptcha"
                class="reCaptcha"
                @verify="on_captcha_verify"
                @expired="on_captcha_expired"
                :sitekey="sharedStore.reCaptchaSiteKey" />

              <button class="mkbtn mkbtn-primary" type="button" @click.prevent="twoFactorConfirm">Save changes</button>
            </div>

          </div>
          <div class="form-input">
            <Checkbox label="Forbid TOR access" />
            <div class="clearfix"></div>
          </div>
        </form>
      </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import { SharedStore } from '@/shared-store';
import { WebApi } from '@/webapi';
import Checkbox from '@/components/elements/Checkbox';
import VueRecaptcha from 'vue-recaptcha';

export default {
  components : {
    Checkbox,
    VueRecaptcha
  },
  data() {
    return {
      sharedStore : SharedStore.state,
      twoFactorQr : null,
      googleAuthCode : null,
      captcha : null
    }
  },
  computed : {
    ...mapState({
      fa2 : state => state.UserSettings.FA2,
      forbidTor : state => state.UserSettings.ForbidTOR
    })
  },
  methods : {
    getWidthForCm(cm_value) {
      /* cm = px * 2.54 / dpi; */
      return cm_value * (window.devicePixelRatio * 96) / 2.54;
    },
    on_captcha_verify(r){
      this.captcha = r;
    },
    on_captcha_expired(){
      this.captcha = null;
    },
    twoFactorChanged(value) {
      let self = this;
      if (value === true) {
        WebApi.turn_2fa_on(function(r){
          if (r.isSuccess) {
            self.twoFactorQr = r.data.base64image;
          }
        });
      }
      if (value === false) {
        WebApi.turn_2fa_off(function(r){
          if (!r.isSuccess) {
            alert(r.message);
          }
        })
      }
    },
    twoFactorConfirm() {
      let self = this;

      self.$refs.reCaptcha.reset();

      if (self.captcha && self.googleAuthCode) {
        WebApi.turn_2fa_on_confirm({ code: self.googleAuthCode, captcha:self.captcha }, 
          function(r){
            if (r.isSuccess) {
              self.twoFactorQr = null;
            } else {
              alert(r.message);
            }
          });
      }
    },
  }
}
</script>

<style>

</style>



