<template>
  <div id="hashrate" class="mk-panel hashrate">
    <div class="hashrate-caption">
      <h2>Hashrate</h2>
      <small>*historical chart</small>
    </div>
    <div class="mk-panel-body">      
      <div class="algorythm-buttons">
        <buttons-group :buttons="algorythms" :onClick="onAlgorythmChanged" />
      </div>
      <div class="clearfix"></div>
      <div class="chart-container">
        <!--
        <canvas id="hashrateHistoricalChart"></canvas>
        -->
        <hashrate-chart :chart-data="datacollection" :options="hashrateOptions" />
      </div>
    </div>
  </div>
</template>

<script>
import MinekampApi from '@/api/MinekampApi';
import ButtonsGroup from '@/components/elements/ButtonsGroup';
import HashrateChart from '@/charts/HashrateChart.js'
import moment from 'moment';

export default {
  components : {
    ButtonsGroup,
    HashrateChart
  },
  data() {
    return {
      algorythms : [
        { id: "sha256", caption: "SHA-256", selected : true },
        { id: "scrypt", caption: "Scrypt" },
        { id: "x11", caption: "x11" }
      ],
      hashrate: [],
      datacollection : null,
      hashrateBackground : null,
      hashrateOptions : {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            xAxes: [{              
              ticks: {
                beginAtZero: true,                
                maxTicksLimit: 5,
                maxRotation: 0
              },
              gridLines: {
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }],
            yAxes: [{
              ticks: {
                beginAtZero:true,
                //autoSkip: true,
                //maxTicksLimit: 5,
                callback: function(tick) {
                  return tick.toString() + ' TH/s';
                }
              },


              gridLines: {                
                zeroLineColor:"#82e6f6",
                color: "transparent",
                display: true,
                drawBorder: false,
                zeroLineWidth: 1
              }
            }]
          },
          elements: { 
            point: { 
              radius: 1 
            } 
          },
          legend: {
            display: false
          },
        }
    }
  },
  created() {    
  },
  mounted() {
    var ctx = document.getElementById("line-chart").getContext('2d');
    this.hashrateBackground = ctx.createLinearGradient(0, 0, 0, 200);
    this.hashrateBackground.addColorStop(0, "#f7fdff");
    this.hashrateBackground.addColorStop(1, "#bdf5ff");

    this.onAlgorythmChanged(this.algorythms[0]);
  },
  methods : {
    onAlgorythmChanged(value) {
      this.getChartData(value.id);
    },
    getChartData(name) {
      let self = this;
      MinekampApi.hashrate(name, function(r){
        //console.log(r);
        let data = [];
        let labels = [];

        for (let i = 0; i < r.length; i++) {
          data.push(r[i].acceptedHashrate/10000000000000000);
          labels.push(moment(r[i].startDate).format('MM/DD/YY'));
        }

        self.datacollection = {
          labels: labels,
          datasets: [{            
            data: data,
            //steppedLine: true,
            backgroundColor: self.hashrateBackground,
            borderColor: '#d4f9ff',
            borderWidth: 1,
            pointRadius: 0
          }]
        }
      });
    },
    fillData () {      
      this.datacollection = {
        labels: [1, 2, 3, 4, 5, 6],
        datasets: [{            
          data: [5, 3, 7, 1, 6, 3, 8, 5],
          backgroundColor: this.hashrateBackground,
          borderColor: '#d4f9ff',
          borderWidth: 1,
          pointRadius: 0
        }]
      }
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/var.scss";

  .hashrate {
    .mk-panel-body {
      @media (min-width: $tablet-min-width) {
        padding-top: 0;
      }
    }
    .hashrate-caption {      
      text-align: left;
      color: #0f0f0f;

      @media (min-width: $tablet-min-width) {
        float: left;
      }

      h2 {
        font-size: 24px;
        font-weight: bold;        
      }
      small {
        display: inline-block;
        padding-top: 15px;
        font-size: 14px;
        text-align: left;
        color: #d8d8d8;
      }
    }
    .algorythm-buttons {
      width: 100%;
      @media (min-width: $tablet-min-width) {
        max-width: 266px;
        float: right;
      }
    }
  }
  #line-chart {
    height: 200px;
  }
  .chart-container {
    position: relative;
    height: 250px;
    padding-top: 40px;
    padding-bottom: 23px;
    overflow: hidden;

    #hashrateHistoricalChart {
      width: 100%;
    }    
  }
  
</style>



