<template>
  <div class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">
        General Enquiries
      </h3>
    </div>
    <div class="mk-panel-body">
      <form class="mk-form" style="position: relative;" autocomplete="on">
        <div class="row clearfix">
          <div class="col-50">
            <div class="mk-input-group">
              <label for="">Your Name (required)</label>
              <input class="mk-panel-input" type="text" v-model="form.name" />
            </div>
          </div>
          <div class="col-50">
            <div class="mk-input-group">
              <label for="">Your E-mail (required)</label>
              <input class="mk-panel-input" type="text" v-model="form.email" />
            </div>
          </div>
        </div>

        <div class="mk-input-group">
          <label for="">Your E-mail (required)</label>
          <textarea class="mk-panel-textarea" rows="10" v-model="form.message"></textarea>
        </div>

        <div class="captcha-with-button">
          <div class="captcha-container">
            <vue-recaptcha
              ref="reCaptcha"
              class="reCaptcha"
              @verify="on_verify"
              @expired="on_expired"
              :sitekey="sharedStore.reCaptchaSiteKey">
            </vue-recaptcha>
          </div>
          <button id="btn-contacts-send" class="mkbtn mkbtn-round-large mkbtn-primary" @click.prevent="sendMessage">Send</button>
        </div>
        <div class="mk-panel-executed" v-if="form.executed.status">
          <div class="btn-modal-close" @click="resetForm"></div>
          <h2 class="success" v-if="form.executed.isSuccess">Success!</h2>
          <h2 class="error" v-if="!form.executed.isSuccess">Error!</h2>
          <h3>{{ form.executed.message }}</h3>
        </div>
      </form>
      <div class="description">
        <p>Please use this General Enquiry Form to send us a message.</p>
        <p>If you would like to open support ticket, please use &quot;<a href="/support">Support</a>&quot; link.</p>
      </div>
      <div class="contacts-socials">
        <h4>Follow us</h4>
        <div class="contacts-social-icons-container">
          <a :href="sharedStore.telegram" target="_blank" class="contacts-social contacts-social-telegram"></a>
          <a :href="sharedStore.twitter" target="_blank" class="contacts-social contacts-social-twitter"></a>
          <a :href="sharedStore.instagram" target="_blank" class="contacts-social contacts-social-instagram"></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { SharedStore } from '@/shared-store';
import { WebApi } from '@/webapi';
import VueRecaptcha from 'vue-recaptcha';

export default {
  data() {
    return {
      sharedStore : SharedStore.state,
      reCaptchaCode : null,
      form : {
        name : null,
        email : null,
        message : null,
        executed : {
          status : false,
          isSuccess : false,
          message : "null"
        }
      }
    }
  },
  components : {
    VueRecaptcha
  },
  methods : {
    on_verify(r) {
      this.$refs.reCaptchaCode = r;
    },
    on_expired() {
      this.$refs.reCaptchaCode = null;
    },
    resetForm() {
      this.form.name = null;
      this.form.email = null;
      this.form.message = null;
      this.form.executed.status = false;
      this.form.executed.message = null;
      this.form.executed.isSuccess = false;
    },
    sendMessage() {
      let self = this;
      self.$refs.reCaptcha.reset();
      WebApi.contact_us({ 
        name : self.form.name,
        email : self.form.email,
        message : self.form.message,
        captcha : this.$refs.reCaptchaCode
     }, function(r){
       self.form.executed.status = true;
       self.form.executed.message = r.message;
       self.form.executed.isSuccess = r.isSuccess;
     });
    }
  }
}
</script>

<style lang="scss">
  a {
    color: #02a6d2;
    text-decoration: underline;
  }
  .mk-input-group {
    margin-top: 40px;
    label {
      font-size: 14px;
      font-weight: 300;
      text-align: left;
      color: #000000;
    }
    .mk-panel-input {
      margin-top: 0;
    }
  }

  .contacts-socials {
    margin-top: 46px;
    text-align: center;

    h4 {
      text-align: center;
    }

    .contacts-social-icons-container {
      
      padding-top: 33px;

      .contacts-social {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-left: 10px;
        margin-right: 10px;
      }
    }
  }
  .description {
    margin-top: 40px;    

    p {
      padding: 0;
      margin: 0;
      font-size: 14px;
      font-weight: 600;
      line-height: 1.79;
      text-align: left;
      color: #000000;
    }
  }

  .contacts-social {
    background-position: 0 0;
    background-repeat: no-repeat;

    &:hover {
      background-position: -20px 0;
    }
  }
  .contacts-social-telegram {
    background-image: url(/static/img/socials/telegram.svg);
  }
  .contacts-social-instagram {
    background-image: url(/static/img/socials/instagram.svg);
  }
  .contacts-social-twitter {
    background-image: url(/static/img/socials/twitter.svg);
  }
  .captcha-container {
    margin: 30px 0px 30px 0px;
    width: 100%;

    @media (min-width: 768px) {
      max-width: 300px;
      display: inline-block;
    }

    .reCaptcha {
      margin-bottom: 0;
      div {
        margin: 0 auto;
        
      }
    }
  }

  #btn-contacts-send {
    width: 100%;
    max-width: 263px;
    margin: 0 auto;
    display: block;

    @media (min-width: 768px) {
      display: inline-block;
      align-self: center;
      margin-left: auto;
    }
  }

  .captcha-with-button {
    @media (min-width: 768px) {
      display: flex;
      text-align: right;      
    }
  }

  .col-50 {
    width: 100%;
    @media (min-width: 768px) {
      width: 50%;
    }
  }
  .mk-form {
    width: 100%;
    max-width: 894px;

    @media (min-width: 768px) {
      display: block;
      border-radius: 3px;
      border: solid 1px #f3f8fc;
      background-color: #ffffff;
      padding: 30px;
    }
    @media (min-width: 1024px) {
      padding: 80px;
    }
  }
  .mk-panel-executed {
    position: absolute; 
    top: 0; 
    left: 0; 
    right: 0; 
    bottom: -20px; 
    background-color: #ffffff;
    padding: 20px;

    @media (min-width: 768px) {
      padding: 50px;
    }
    .btn-modal-close {
      position: absolute;
      right: 28px;
      top: 28px;
      width: 40px;
      height: 40px;
      cursor: pointer;
      background-image: url('/static/img/close.svg');
      background-position: center;
      background-repeat: no-repeat;
    }

    h2.error {
      color: #ef2b04;
      font-size: 30px;
      margin-bottom: 48px;
      font-weight: 800;
    }
    h2.success {
      color: #39d475;
      font-size: 30px;
      margin-bottom: 48px;
      font-weight: 800;
    }
  }
</style>



