<template>
  <div id="app">
    <header-menu />
    <div class="container">
      <header-currency />
    </div>
    <div class="container body-content">
      <cabinet-menu-mobile v-if="authState.isAuthenticated" />
      <router-view />
    </div>
    <div class="footer">
      <div class="footer-primary-dark">
        <div class="container">
          <p>&copy;&nbsp;MineKamp 2018</p>
          <p class="reserved-rights"> 2018 All rights reserved</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { AuthStore } from './auth-store';

  import CabinetMenuMobile from '@/components/CabinetMenuMobile';
  import HeaderMenu from '@/components/HeaderMenu';
  import HeaderCurrency from '@/components/HeaderCurrency';

  export default {
    name: 'App',
    components: {
      HeaderMenu,
      CabinetMenuMobile,
      HeaderCurrency
    },
    data() {
      return {
        authState : AuthStore.state
      }
    },
    mounted() {

    },
    methods : {
    }
  }
</script>

<style lang="scss">
    $fa-font-path : "~@fortawesome/fontawesome-free-webfonts/webfonts";
    @import "~@fortawesome/fontawesome-free-webfonts/scss/fontawesome.scss";
    @import "~@fortawesome/fontawesome-free-webfonts/scss/fa-solid.scss";
    @import "~@fortawesome/fontawesome-free-webfonts/scss/fa-regular.scss";
    @import "~@fortawesome/fontawesome-free-webfonts/scss/fa-brands.scss";

    .footer {
      p {
        display: inline-block;
      }
      .reserved-rights {
        float: right;
        font-size: 13px;  
        color: #d0eff7;
      }
    }
    
</style>



