<template>
  <div class="mk-panel">
    <div class="mk-panel-header">
      <h3 class="mk-panel-title">Cool mining calculator</h3>
    </div>
    <div class="mk-panel-body">
      <!--line-chart :dataset="chartData" :ylabels="labels"></line-chart-->
      <canvas id="coinPrice" />
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'

export default {
  data() {
    return {
      coinPrice : {
        data : [],
        labels : []
      }
    }
  },
  created() {
    for (let i = 0; i < 30; i++) {
      this.coinPrice.data.push(14000 + i * 10);
      this.coinPrice.labels.push("");
    }
  },
  mounted() {
    var ctx = document.getElementById("coinPrice").getContext('2d');

    var gradientStroke = ctx.createLinearGradient(0, 0, 0, 200);
    gradientStroke.addColorStop(0, "#f7fdff");
    gradientStroke.addColorStop(1, "#bdf5ff");

    var myChart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: this.coinPrice.labels,
        datasets: [{                      
          data: this.coinPrice.data,
          backgroundColor: gradientStroke,
          borderColor: '#d4f9ff',
          borderWidth: 1,
          pointRadius: 5,
          pointBackgroundColor: '#2691cd'
        }]
      },
      options : {
        legend: {
          display: false
        },
        responsive : true
      },
      scales : {
        xAxes: [{
          ticks: {
            beginAtZero:true
          },
          gridLines: {
            zeroLineColor:"#82e6f6",
            color: "transparent",
            display: true,
            drawBorder: false,
            zeroLineWidth: 1
          }
        }]
      }
    });
  },
  methods : {
    
  }
}
</script>

<style lang="scss">
  #coinPrice {
    width: 371px;
    height: 175px;
  }
</style>



