<template>
  <div class="bordered-select bordered-select--collapsed" @click="toggle">
    <div class="current-item">
      <input type="text" class="visually-hidden" />
      <span v-if="currentItem">{{currentItem.name}}</span>
    </div>
    <div class="chevron">
      <div class="chevron__icon"></div>
    </div>
    <div class="values">
      <div class="values--item" v-for="item in items" :key="item.id" @click="onItemClick(item, $event)">
        <input type="text" class="visually-hidden" />
        <span>{{item.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props : {
    items : Array,
    onItemClicked : Function
  },
  data() {
    return {
      currentItem : null
    }
  },
  created() {
    if (!this.currentItem && this.items && this.items.length > 0) {
      this.currentItem = this.items[0];
    }
  },
  methods : {
    toggle(e) {
      $(e.currentTarget)
        .toggleClass('bordered-select--collapsed')
        .toggleClass('bordered-select--expanded');
    },
    onItemClick(item, e) {

      if (this.onItemClicked) {
        onItemClicked(item);
      }
    }
  }
}
</script>

<style lang="scss">
  @import "@/styles/var.scss";
  @import "../../styles/common.scss";

  .bordered-select {
    @include selection-none;
    position: relative;
    min-width: 200px;
    width: 100%;
    height: 37px;
    border-radius: 3px;
    background-color: #ffffff;
    border: solid 1px #c8e8f0;
    font-size: 13px;
    z-index: 1;
    cursor: pointer;

    &-sm {
      height: 26px;

      .current-item {
        padding-top: 5px!important;
      }
      .chevron {
        .chevron__icon {
          width: 30px!important;
          height: 25px!important;

          &::before {        
            top: 12px!important;
            left: 8px!important;
          }
          &::after {
            top: 12px!important;
            left: 13px!important;
          }
        }
      }
      .values {
        top: 24px!important;
      }
    }

    &--collapsed {
      .chevron__icon {
        transform: rotate(0);
        transition: 100ms linear;
      }
      .values {
        display: none;
      }
    }

    &--expanded {
      .chevron__icon {
        transform: rotate(-180deg);
        transition: 100ms linear;
      }
      .values {
        display: block;
      }
    }

    .current-item {
      padding-top: 10px;
      padding-left: 12px;
      text-align: left!important;
    }

    .chevron {
      position: absolute;
      top: 0;
      right: 0;      
      border-left: solid 1px #c8e8f0;

      .chevron__icon {
        position: relative;
        width: 36px;
        height: 36px;

        &::before {        
          content: '';
          position: absolute;
          top: 18px;
          left: 11px;
          //display: block;
          width: 8px;
          height: 2px;
          background-color: #000000;
          transform: rotate(45deg);
        }
        &::after {
          content: '';
          position: absolute;
          top: 18px;
          left: 16px;
          //display: block;
          width: 8px;
          height: 2px;
          background-color: #000000;
          transform: rotate(-45deg);
        }
      }
    }

    .values {
      position: absolute;
      top: 35px;
      left: 0;
      right: 0;
      padding-top: 6px;
      padding-bottom: 6px;
      background-color: #ffffff;
      border: solid 1px #c8e8f0;

      .values--item {
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 12px;
        padding-right: 12px;

        &:hover {
          color: #ffffff;
          background-color: $color-focused;
        }
      }
    }
  }
</style>



