<template>
  <div class="mk-btn-group clearfix">
    <button 
      type="button"
      v-for="btn in buttons" 
      :key="btn.id" 
      :class="{ active : btn.selected }"
      @click="onClicked(btn, $event)">
      {{btn.caption}}
    </button>
  </div>
</template>

<script>
export default {
  props : {
    buttons : Array,
    onClick : Function
  },
  methods: {
    onClicked(btn, e) {
      let jq_sender = $(e.currentTarget);
      jq_sender.parent().find('button').removeClass('active');
      jq_sender.addClass('active');
      this.onClick(btn);
    }
  }
}
</script>

<style>

</style>



